import React, { useContext, useEffect, useState, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import classContext from "../../context/classContext";

function StdClassTab({ searchQuery }) {
    const navigate = useNavigate();

    // Memoize the Imgarr array
    const Imgarr = useMemo(() => [
        "/images/img1.png",
        "/images/img2.png",
        "/images/img3.png",
        "/images/img4.png",
        "/images/img5.png",
        "/images/img6.png",
        "/images/img7.png",
        "/images/img8.png",
        "/images/img9.png",
    ], []); // Empty dependency array means Imgarr will only be initialized once

    const { classes, getjoinedClass } = useContext(classContext);
    const [classListWithImages, setClassListWithImages] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                if (!classes || classes.length === 0) {
                    await getjoinedClass();
                    console.log("Fetching joined classes...");
                }
            } catch (err) {
                alert("Failed to fetch classes");
            }
        };
        fetchData();
    }, [classes, getjoinedClass]); // Only depend on classes and getjoinedClass

    useEffect(() => {
        if (classes?.length > 0) {
            const updatedClassList = classes.map((classItem, index) => ({
                ...classItem,
                randomImage: Imgarr[index % Imgarr.length], // Imgarr is now memoized
            }));
            setClassListWithImages(updatedClassList);
        }
    }, [classes, Imgarr]); // Add Imgarr as a dependency here

    const handleClass = (classId) => {
        navigate("/stdgetclass", { state: { classId } });
    };

    // Filter the classes based on search query
    const filteredClasses = classListWithImages.filter((classItem) =>
        classItem.className.toLowerCase().includes(searchQuery.toLowerCase())
    );

    return (
        <div className="grid grid-cols-1 sm:grid-cols-1 lg:grid-cols-1 gap-4 mt-6">
            {filteredClasses.length > 0 ? (
                filteredClasses.map((classItem, index) => (
                    <div key={index} className="grid grid-cols-1 sm:grid-cols-1 lg:grid-cols-1 gap-4">
                        <form
                            className="p-6 bg-[#A0AEC0] border-[#D6BCFA] rounded-lg shadow-[#3B82F6] hover:shadow-lg transition-shadow relative"
                            style={{
                                backgroundImage: `url(${classItem.randomImage})`,
                                backgroundSize: "cover",
                                backgroundRepeat: "no-repeat",
                                backgroundPosition: "center",
                            }}
                            onClick={() => handleClass(classItem._id)}
                        >
                            {/* Content */}
                            <h5 className="text-2xl font-bold text-gray-900 dark:text-white">
                                {classItem.className}
                            </h5>
                            <p className="text-gray-700">{classItem.branch}</p>

                            {/* Subject positioned in the middle-right */}
                            <div className="absolute md:top-1/2 top-1/3 right-4 transform -translate-y-1/2">
                                <h1 className="text-3xl md:text-5xl font-bold text-gray-900 dark:text-white md:-mt-6">
                                    {classItem.subject}
                                </h1>
                                <p className="text-gray-700 text-right">{classItem.inviteCode}</p>
                            </div>
                        </form>
                    </div>
                ))
            ) : (
                <p className="text-gray-700 text-center mt-4">No classes found</p>
            )}
        </div>
    );
}

export default StdClassTab;
