import React, { useState, useEffect } from "react";

const ProfileModal = ({ onClose, onSave }) => {
  const [photo, setPhoto] = useState(null);
  const [isVisible, setIsVisible] = useState(false);
  const [isDragging, setIsDragging] = useState(false);

  useEffect(() => {
    const savedPhoto = localStorage.getItem("profilePhoto");
    if (savedPhoto) {
      setPhoto(savedPhoto);
    }
    setIsVisible(true);
  }, []);

  const handlePhotoUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        setPhoto(e.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSave = () => {
    if (photo) {
      localStorage.setItem("profilePhoto", photo);
      setIsVisible(false);
      setTimeout(() => {
        onSave(photo);
        onClose();
      }, 300);
    } else {
      alert("Please select a photo first");
    }
  };

  const handleClose = () => {
    setIsVisible(false);
    setTimeout(onClose, 300);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragging(false);
    const file = e.dataTransfer.files[0];
    if (file && file.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.onload = (e) => {
        setPhoto(e.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <div 
      className={`fixed inset-0 flex items-center justify-center bg-gray-900/75 backdrop-blur-sm transition-opacity duration-300 z-50
        ${isVisible ? 'opacity-100' : 'opacity-0'}`}
      onClick={handleClose}
    >
      <div 
        className={`bg-white rounded-xl shadow-2xl w-96 transform transition-all duration-300
          ${isVisible ? 'scale-100 translate-y-0' : 'scale-95 translate-y-4'}
          ${isDragging ? 'ring-2 ring-blue-500' : ''}`}
        onClick={e => e.stopPropagation()}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
      >
        <div className="p-6">
          <h2 className="text-2xl font-semibold text-gray-800 mb-6">Profile Photo</h2>
          
          <div className="mb-6">
            {photo ? (
              <div className="relative w-32 h-32 mx-auto">
                <img
                  src={photo}
                  alt="Profile preview"
                  className="w-32 h-32 rounded-full object-cover ring-4 ring-gray-100 shadow-lg transition-transform duration-300 hover:scale-105"
                />
              </div>
            ) : (
              <div className="w-32 h-32 mx-auto rounded-full bg-gray-100 flex items-center justify-center">
                <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
              </div>
            )}
          </div>

          <div 
            className={`border-2 border-dashed rounded-lg p-4 mb-6 text-center transition-colors
              ${isDragging ? 'border-blue-500 bg-blue-50' : 'border-gray-300 hover:border-gray-400'}`}
          >
            <input
              type="file"
              accept="image/*"
              onChange={handlePhotoUpload}
              className="hidden"
              id="photo-upload"
            />
            <label 
              htmlFor="photo-upload"
              className="cursor-pointer text-sm text-gray-600 hover:text-gray-800"
            >
              Click to upload or drag and drop your photo here
            </label>
          </div>

          <div className="flex justify-end space-x-3">
            <button
              onClick={handleClose}
              className="px-4 py-2 text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
            >
              Cancel
            </button>
            <button
              onClick={handleSave}
              disabled={!photo}
              className={`px-4 py-2 text-white rounded-lg transition-all duration-200
                ${photo 
                  ? 'bg-blue-500 hover:bg-blue-600 active:scale-95' 
                  : 'bg-blue-300 cursor-not-allowed'}`}
            >
              Save Photo
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileModal;