import React, { useState } from "react";
import attendanceContext from "./attendanceContext";
import Cookies from "js-cookie"; 

const AttendanceState = ({ children }) => {
    const host = "https://presence-pro-backend.vercel.app";
    const [attendance, setAttendance] = useState([]);
    const token = localStorage.getItem("authToken");

    // Fetch all attendance records
    const getAttendance = async () => {
        try {
            const response = await fetch(`${host}/api/attendance/fetchallattendance`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
            });

            if (!response.ok) throw new Error("Failed to fetch attendance records.");

            const data = await response.json();
            setAttendance(data);
        } catch (error) {
            console.error("Error fetching attendance:", error);
        }
    };

    // Fetch all attendance records for student
    const getStdAttendance = async () => {
        try {
            const response = await fetch(`${host}/api/attendance/stdfetchallattendance`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
            });

            if (!response.ok) throw new Error("Failed to fetch attendance records.");

            const data = await response.json();
            setAttendance(data);
        } catch (error) {
            console.error("Error fetching attendance:", error);
        }
    };

  

    // Add a new attendance record
    const addAttendance = async (className, subject, date, attendacneCode) => {
        try {
            const response = await fetch(`${host}/api/attendance/addattendance`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
                body: JSON.stringify({ className, subject, date, attendacneCode }),
            });

            if (!response.ok) { 
                const errorResponse = await response.json();
                throw new Error(errorResponse.error || "Failed to add attendance record.");
            }

            const newAttendance = await response.json();
            Cookies.set("TeacherattendanceUrl", newAttendance.url, { expires: 1 });
            setAttendance((prevAttendance) => [...prevAttendance, newAttendance]);
        } catch (error) {
            console.error("Error adding attendance:", error);
            throw error; // Propagate error to the caller
        }
    };


    // Delete an attendance record
    const deleteAttendance = async (id) => {
        try {
            const response = await fetch(`${host}/api/attendance/deleteattendance/${id}`, {
                method: "DELETE",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
            });

            if (!response.ok) throw new Error("Failed to delete attendance record.");

            await response.json();
            setAttendance((prevAttendance) =>
                prevAttendance.filter((record) => record._id !== id)
            );
        } catch (error) {
            console.error("Error deleting attendance:", error);
        }
    };

    // Edit an existing attendance record
    const editAttendance = async (attendanceData) => {
        try {
            const { id, EclassName, Esubject, Edate, EattendanceCode } = attendanceData;

            if (!id || typeof id !== 'string') {
                throw new TypeError('Invalid ID: ID must be a non-empty string.');
            }

            const trimmedId = id.trim(); // Ensure id is a trimmed string
            const response = await fetch(`/api/attendance/${trimmedId}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    className: EclassName,
                    subject: Esubject,
                    date: Edate,
                    attendanceCode: EattendanceCode,
                }),
            });

            if (!response.ok) {
                throw new Error(`Failed to update attendance: ${response.statusText}`);
            }

            const updatedAttendance = await response.json();
            // Update state with the new attendance
            setAttendance((prev) =>
                prev.map((item) => (item._id === trimmedId ? updatedAttendance : item))
            );
        } catch (error) {
            console.error('Error updating attendance:', error);
        }
    };


    return (
        <attendanceContext.Provider
            value={{
                attendance,
                setAttendance,
                getAttendance,
                getStdAttendance,
                addAttendance,
                deleteAttendance,
                editAttendance,
            }}
        >
            {children}
        </attendanceContext.Provider>
    );
};

export default AttendanceState;
