import { useContext, useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import StdMicroprojectContext from "../../context/StdMicroprojectContext";
import Microlist from "../Teacher/Microlist";

const AddMicroproject = () => {
    const { addMicroproject, loading, error, message } = useContext(StdMicroprojectContext);
    const location = useLocation();
    const classIdFromState = location.state?.classId || "";

    const [formData, setFormData] = useState({
        title: "",
        classId: classIdFromState,
        students: [{ name: "", rollNo: "", class: "", enrollment: "" }],
    });

    useEffect(() => {
        if (classIdFromState) {
            setFormData((prev) => ({ ...prev, classId: classIdFromState }));
        }
    }, [classIdFromState]);

    const handleChange = (e, index, field) => {
        const updatedStudents = [...formData.students];
        updatedStudents[index][field] = e.target.value;
        setFormData({ ...formData, students: updatedStudents });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        addMicroproject(formData);
    };

    const handleAddStudent = () => {
        if (formData.students.length < 5) {
            setFormData({
                ...formData,
                students: [...formData.students, { name: "", rollNo: "", class: "", enrollment: "" }],
            });
        }
    };

    return (
        <>
            <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
                <div className="w-full bg-white shadow-2xl rounded-2xl p-6 border border-blue-100 transform transition-all duration-300 hover:scale-[1.01]">
                    <h2 className="text-3xl font-bold text-blue-800 mb-6 text-center relative after:absolute after:bottom-[-10px] after:left-1/2 after:transform after:translate-x-[-50%] after:h-1 after:w-16 after:bg-blue-500">
                        Create Microproject
                    </h2>

                    {/* Animated Error Message */}
                    {error && (
                        <div className="bg-red-50 border-l-4 border-red-400 text-red-700 px-4 py-3 rounded relative mb-4 animate-shake">
                            <p className="font-semibold">Error</p>
                            {error}
                        </div>
                    )}

                    {/* Animated Success Message */}
                    {message && (
                        <div className="bg-green-50 border-l-4 border-green-400 text-green-700 px-4 py-3 rounded relative mb-4 animate-bounce-in">
                            <p className="font-semibold">Success</p>
                            {message}
                        </div>
                    )}

                    <form onSubmit={handleSubmit} className="space-y-6">
                        {/* Project Title */}
                        <div className="group">
                            <label className="block text-gray-700 text-sm font-semibold mb-2 group-focus-within:text-blue-600 transition-colors">
                                Project Title
                            </label>
                            <input
                                type="text"
                                value={formData.title}
                                onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300 hover:shadow-sm"
                                placeholder="Enter project title"
                                required
                            />
                        </div>

                        {/* Student Details */}
                        <div className="space-y-4">
                            <div className="flex justify-between items-center mb-4">
                                <h3 className="text-xl font-semibold text-gray-800 animate-pulse-slow">
                                    Student Information
                                </h3>
                                {formData.students.length < 5 && (
                                    <button
                                        type="button"
                                        onClick={handleAddStudent}
                                        disabled={formData.students[formData.students.length - 1].name === ""}
                                        className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-300 transform hover:scale-105 active:scale-95"
                                    >
                                        + Add Student
                                    </button>
                                )}
                            </div>

                            {formData.students.map((student, index) => (
                                <div
                                    key={index}
                                    className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-3 p-4 bg-blue-50 rounded-md transition-all duration-300 hover:bg-blue-100"
                                >
                                    <input
                                        type="text"
                                        placeholder="Name"
                                        value={student.name}
                                        onChange={(e) => handleChange(e, index, "name")}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                        required
                                    />
                                    <input
                                        type="text"
                                        placeholder="Roll No"
                                        value={student.rollNo}
                                        onChange={(e) => handleChange(e, index, "rollNo")}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                        required
                                    />
                                    <input
                                        type="text"
                                        placeholder="Class"
                                        value={student.class}
                                        onChange={(e) => handleChange(e, index, "class")}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                        required
                                    />
                                    <input
                                        type="text"
                                        placeholder="Enrollment"
                                        value={student.enrollment}
                                        onChange={(e) => handleChange(e, index, "enrollment")}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                        required
                                    />
                                </div>
                            ))}
                        </div>

                        {/* Submit Button */}
                        <button
                            type="submit"
                            disabled={loading}
                            className="w-full py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-300 transform hover:scale-[1.02] active:scale-95"
                        >
                            {loading ? "Adding..." : "Create Microproject"}
                        </button>
                    </form>
                </div>
            </main>
            <Microlist />
        </>
    );
};

export default AddMicroproject;