import React, { useState } from "react";
import cloudinaryContext from "./cloudinaryContext";

const CloudinaryState = ({ children }) => {
    const host = "https://presence-pro-backend.vercel.app";
    const token = localStorage.getItem("authToken");
    const uploadInitial = [];
    const [uploads, setUploads] = useState(uploadInitial);

    // Function to get all uploads
    const getUploads = async () => {
        try {
            const response = await fetch(`${host}/api/upload/fetchalluploads`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
            });

            if (!response.ok) {
                throw new Error("Failed to fetch uploads.");
            }

            const json = await response.json();
            setUploads(json);
        } catch (error) {
            console.error("Error fetching uploads:", error);
        }
    };

    // Function to upload a file
    const uploadFile = async (file) => {
        const formData = new FormData();
        formData.append("file", file);

        try {
            const response = await fetch(`${host}/api/upload/uploadfile`, {
                method: "POST",
                headers: {
                    "auth-token": token,
                },
                body: formData,
            });

            if (!response.ok) {
                throw new Error("Failed to upload file.");
            }

            const newUpload = await response.json();
            setUploads((prevUploads) => [...prevUploads, newUpload]);
        } catch (error) {
            console.error("Error uploading file:", error);
        }
    };

    return (
        <cloudinaryContext.Provider value={{ getUploads, uploadFile, uploads }}>
            {children}
        </cloudinaryContext.Provider>
    );
};

export default CloudinaryState;