import React, { useState } from "react";
import classContext from "./classContext";

const ClassState = ({ children }) => {
    const host = "https://presence-pro-backend.vercel.app";
    const token = localStorage.getItem("authToken");
    const classInitial = [];
    const [classes, setClasses] = useState(classInitial);


    // NO 1: Get all classes
    const getClass = async () => {
        try {
            const response = await fetch(`${host}/api/class/fetchallclass`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
            });

            if (!response.ok) {
                throw new Error("Failed to fetch classes.");
            }

            const json = await response.json();
            setClasses(json);
        } catch (error) {
            console.error("Error fetching classes:", error);
        }
    };

    const getjoinedClass = async () => {
        try {
            const response = await fetch(`${host}/api/class/joinedclasses`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
            });

            if (!response.ok) {
                throw new Error("Failed to fetch classes.");
            }

            const json = await response.json();
            setClasses(json);
        } catch (error) {
            console.error("Error fetching classes:", error);
        }
    };

    const JoinClass = async (inviteCode) => {
        try {
            console.log('Joining class with invite code:', inviteCode); // Debugging log

            const response = await fetch(`${host}/api/class/joinclass`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
                body: JSON.stringify({ inviteCode }),
            });

            const jsonResponse = await response.json();
            console.log('Server response:', jsonResponse);

            if (!response.ok || jsonResponse.error) {
                throw new Error(jsonResponse.error || "Failed to add class.");
            }

            setClasses((prevClasses) => [...prevClasses, jsonResponse]); // Assuming the server returns the class data
            return true; // Indicating success
        } catch (error) {
            console.error("Error adding class:", error);
            return false; // Indicating failure
        }
    };



    // NO 2: Add Class
    const addClass = async (className, subject, inviteCode, branch) => {
        try {
            const response = await fetch(`${host}/api/class/addclass`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
                body: JSON.stringify({ className, subject, inviteCode, branch }),
            });

            if (!response.ok) {
                throw new Error("Failed to add class.");
            }

            const newClass = await response.json();
            setClasses((prevClasses) => [...prevClasses, newClass]);
        } catch (error) {
            console.error("Error adding class:", error);
        }
    };

    // NO 3: Delete Class
    const deleteClass = async (id) => {
        try {
            const response = await fetch(`${host}/api/class/deleteclass/${id}`, {
                method: "DELETE",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": token,
                },
            });

            if (!response.ok) {
                throw new Error("Failed to delete class.");
            }

            await response.json(); // Handle response if needed
            setClasses((prevClasses) => prevClasses.filter((cls) => cls._id !== id));
        } catch (error) {
            console.error("Error deleting class:", error);
        }
    };

    return (
        <classContext.Provider value={{ addClass, getClass, setClasses, deleteClass, getjoinedClass, JoinClass, classes }}>
            {children}
        </classContext.Provider>
    );
};

export default ClassState;