import React, { useEffect, useRef, useState } from "react";
import Cookies from "js-cookie";
import Iteam1 from "../static/Iteam1";
import Tab from "../static/Tab";
import CodeForm from "./CodeForm";

const StdHome = () => {
  const scriptURL = Cookies.get("attendanceUrl");
  const formRef = useRef(null);
  const [sheetNames, setSheetNames] = useState([]);
  const [loading, setLoading] = useState(false);
  const [canMarkAttendance, setCanMarkAttendance] = useState(false);
  const [formData, setFormData] = useState({
    rollNo: "",
    date: "",
    sheetName: ""
  });

  // Load sheet names from backend
  useEffect(() => {
    const loadSheetNames = async () => {
      if (!scriptURL) return; // Prevent running if scriptURL is undefined
      setLoading(true);
      try {
        const response = await fetch(`${scriptURL}?action=getSheets`);
        const data = await response.json();
        if (data.sheetNames) {
          setSheetNames(data.sheetNames);
        }
      } catch (error) {
        console.error("Error loading sheet names!", error);
      } finally {
        setLoading(false);
      }
    };
    loadSheetNames();
  }, [scriptURL]);

  // Handle form input changes
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!formData.rollNo || !formData.date || !formData.sheetName) {
      alert("Error: Roll No, Date, and Sheet Name are required!");
      return;
    }

    try {
      // Convert date to DD/MM/YYYY format
      const dateObj = new Date(formData.date);
      const day = String(dateObj.getDate()).padStart(2, '0');
      const month = String(dateObj.getMonth() + 1).padStart(2, '0');
      const year = dateObj.getFullYear();
      const formattedDate = `${day}/${month}/${year}`;

      // Create FormData and append values
      const formDataToSend = new FormData();
      formDataToSend.append("rollNo", formData.rollNo);
      formDataToSend.append("date", formattedDate);
      formDataToSend.append("sheetName", formData.sheetName);

      const response = await fetch(scriptURL, {
        method: "POST",
        body: formDataToSend,
      });

      const result = await response.json();
      if (result.result === "success") {
        alert("Success: " + (result.message || "Attendance marked successfully!"));
        setCanMarkAttendance(false);
        setFormData({ rollNo: "", date: "", sheetName: "" }); // Reset form after success
      } else {
        alert("Error: " + result.error);
      }
    } catch (error) {
      console.error("Error!", error.message);
    }
  };

  return (
    <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
      <Iteam1 />
      <div className="grid grid-cols-1 md:grid-cols-2 md:mt-7 mt-4 md:gap-6 gap-3 pt-3 w-full text-gray-700 bg-[#A0AEC0] shadow-lg rounded-xl bg-clip-border md:p-6 p-3 ">
        <CodeForm onSuccess={() => setCanMarkAttendance(true)} />
        <div className="relative flex flex-col w-full mx-auto text-gray-700 bg-[#2D3748] shadow-lg rounded-xl bg-clip-border">
          <h2 className="text-xl sm:text-2xl font-semibold text-white pt-4 text-center">
            Mark Attendance
          </h2>
          <form ref={formRef} className="grid grid-cols-1 gap-4 p-4 lg:p-6" onSubmit={handleSubmit}>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              <input
                name="rollNo"
                value={formData.rollNo}
                onChange={handleChange}
                placeholder="Roll No"
                required
                className="w-full px-3 py-2.5 text-sm bg-gray-100 border rounded-lg focus:ring-2 focus:ring-blue-500"
              />
              <input
                type="date"
                name="date"
                value={formData.date}
                onChange={handleChange}
                required
                className="w-full px-3 py-2.5 text-sm bg-gray-100 border rounded-lg focus:ring-2 focus:ring-blue-500"
              />
            </div>
            <select
              name="sheetName"
              value={formData.sheetName}
              onChange={handleChange}
              required
              className="w-full px-3 py-2.5 text-sm bg-gray-100 border rounded-lg focus:ring-2 focus:ring-blue-500"
            >
              <option value="">Select Sheet</option>
              {loading ? <option disabled>Loading sheets...</option> : sheetNames.map((name, index) => (
                <option key={index} value={name}>{name}</option>
              ))}
            </select>
            <button
              type="submit"
              disabled={!canMarkAttendance}
              className={`w-full px-4 py-3 mt-1 text-sm font-bold text-white uppercase ${canMarkAttendance ? "bg-blue-600 hover:bg-blue-700" : "bg-gray-500 cursor-not-allowed"} rounded-lg shadow-md focus:ring-2 focus:ring-blue-400`}
            >
              Add Attendance
            </button>
          </form>
        </div>
      </div>
      <Tab />
    </main>
  );
};

export default StdHome;