import React, { useState, useContext } from 'react'; 
import StdClassTab from './StdClassTab';
import classContext from '../../context/classContext';

function StdClass() {
  const { JoinClass } = useContext(classContext);
  const [inviteCode, setInviteCode] = useState('');
  const [searchQuery, setSearchQuery] = useState('');
  const [error, setError] = useState('');

  const handleInviteSubmit = async (e) => {
    e.preventDefault();

    if (!inviteCode) {
      alert(' Invite code is required.');
      return;
    }

    try {
      const success = await JoinClass(inviteCode);

      if (success) {
        setError('');
        setInviteCode('');
        alert(' Successfully joined the class!');
      } else {
        alert(' Invalid invite code or unable to join class.');
      }
    } catch (error) {
      alert(' An error occurred while trying to join the class.');
      console.error('Error joining class:', error);
    }
  };

  return (
    <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
      <div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
          {/* Search Bar */}
          <div className="relative">
            <input
              type="text"
              placeholder="Search classes..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full px-4 py-3 text-sm text-gray-700 bg-white border rounded-xl shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
            />
            <svg
              className="absolute right-4 top-3.5 h-4 w-4 text-gray-400"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
          </div>

          {/* Invite Code Form */}
          <form onSubmit={handleInviteSubmit} className="flex gap-3">
            <input
              type="text"
              placeholder="Enter invite code to join class"
              value={inviteCode}
              onChange={(e) => setInviteCode(e.target.value)}
              className="flex-1 px-4 py-3 text-sm text-gray-700 bg-white border rounded-xl shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
              required
            />
            <button
              type="submit"
              className="px-6 py-3 text-sm font-semibold text-white bg-blue-600 rounded-xl shadow-sm hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 transition-all whitespace-nowrap"
            >
              Join Class
            </button>
          </form>

          {/* Display error message if any */}
          {error && <p className="text-red-600 text-sm mt-2">{error}</p>}
        </div>

        {/* Joined Classes Title */}
        <h2 className="text-2xl font-semibold text-center text-gray-900 mb-6">
          Joined Classes
        </h2>

        {/* Pass searchQuery to StdClassTab */}
        <StdClassTab searchQuery={searchQuery} />
      </div>
    </main>
  );
}

export default StdClass;
