import React, { useState } from "react";

const TabsComponent = () => {
  const [activeTab, setActiveTab] = useState("about");

  return (
    <div className="w-full bg-white border border-gray-200 rounded-xl shadow dark:bg-[#A0AEC0] md:mt-7 mt-4 ">
      <ul
        className="flex flex-wrap text-sm font-medium text-center text-gray-500 border-b border-gray-200 rounded-t-lg bg-gray-50 dark:border-gray-700 dark:text-gray-400 dark:bg-gray-800"
        role="tablist"
      >
        <li className="me-2">
          <button
            type="button"
            role="tab"
            aria-selected={activeTab === "about"}
            className={`inline-block p-4 ${
              activeTab === "about"
                ? "text-blue-600 rounded-ss-lg dark:text-blue-500"
                : "hover:text-gray-600 dark:hover:text-gray-300"
            }`}
            onClick={() => setActiveTab("about")}
          >
            About
          </button>
        </li>
        <li className="me-2">
          <button
            type="button"
            role="tab"
            aria-selected={activeTab === "services"}
            className={`inline-block p-4 ${
              activeTab === "services"
                ? "text-blue-600 dark:text-blue-500"
                : "hover:text-gray-600 dark:hover:text-gray-300"
            }`}
            onClick={() => setActiveTab("services")}
          >
            Services
          </button>
        </li>
      </ul>
      <div>
        {activeTab === "about" && (
          <div
            className="p-4 bg-white rounded-lg md:p-8 dark:bg-[#A0AEC0]"
            role="tabpanel"
          >
            <h2 className="mb-3 text-3xl font-extrabold tracking-tight text-gray-900">
              QuikRoll: Streamlined Attendance, Fast and Easy
            </h2>
            <p className="mb-3 text-gray-800">
              Welcome to the GPJ-Attendance system! This application is designed
              to streamline the attendance tracking process for computer
              classes, ensuring that teachers can easily manage and record
              attendance for various classes such as CO-3K, CO-4K, CO-5I, and
              CO-6I.
            </p>
          </div>
        )}
        {activeTab === "services" && (
          <div
            className="p-4 bg-white rounded-lg md:p-8 dark:bg-[#A0AEC0]"
            role="tabpanel"
          >
            <h2 className="mb-5 text-2xl font-extrabold tracking-tight text-gray-900 ">
              QuikRoll: Revolutionizing Attendance with Speed and Precision
            </h2>
            <ul className="space-y-4 text-gray-800">
              {[
                "Dynamic reports and dashboards",
                "Quick Attendance Marking",
                "Mobile-Friendly Design",
                "Custom Class and Subject Management",
                ].map((service, index) => (
                <li key={index} className="flex space-x-2 items-center">
                  <svg
                    className="flex-shrink-0 w-3.5 h-3.5 text-blue-600 dark:text-blue-500"
                    aria-hidden="true"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path d="M10 .5a9.5 9.5 0 1 0 9.5 9.5A9.51 9.51 0 0 0 10 .5Zm3.707 8.207-4 4a1 1 0 0 1-1.414 0l-2-2a1 1 0 0 1 1.414-1.414L9 10.586l3.293-3.293a1 1 0 0 1 1.414 1.414Z" />
                  </svg>
                  <span className="leading-tight">{service}</span>
                </li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </div>
  );
};

export default TabsComponent;
