import React, { useState, useEffect, useCallback } from "react";
import { useLocation } from "react-router-dom";

const StdGetClass = () => {
    const host = "https://presence-pro-backend.vercel.app";
    const location = useLocation();
    const classId = location.state?.classId;

    const [uploads, setUploads] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [selectedFile, setSelectedFile] = useState(null);

    const fetchUploads = useCallback(async () => {
        setLoading(true);
        setError(null);
        try {
            const response = await fetch(`${host}/api/upload/fetchalluploads/${classId}`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                },
            });
            if (!response.ok) throw new Error("Failed to fetch uploads.");
            const json = await response.json();
            setUploads(json);
        } catch (error) {
            setError(error.message);
        } finally {
            setLoading(false);
        }
    }, [classId, host]);

    useEffect(() => {
        if (classId) fetchUploads();
    }, [classId, fetchUploads]);

    const getFileTypeInfo = (fileName) => {
        const extension = fileName.split('.').pop().toLowerCase();
        const fileTypes = {
            pdf: { icon: "", bgColor: "bg-red-50", textColor: "text-red-600", type: "document" },
            doc: { icon: "", bgColor: "bg-blue-50", textColor: "text-blue-600", type: "document" },
            docx: { icon: "", bgColor: "bg-blue-50", textColor: "text-blue-600", type: "document" },
            xls: { icon: "", bgColor: "bg-green-50", textColor: "text-green-600", type: "document" },
            xlsx: { icon: "", bgColor: "bg-green-50", textColor: "text-green-600", type: "document" },
            jpg: { icon: "", bgColor: "bg-purple-50", textColor: "text-purple-600", type: "image" },
            jpeg: { icon: "", bgColor: "bg-purple-50", textColor: "text-purple-600", type: "image" },
            png: { icon: "", bgColor: "bg-purple-50", textColor: "text-purple-600", type: "image" },
            gif: { icon: "", bgColor: "bg-purple-50", textColor: "text-purple-600", type: "image" },
            zip: { icon: "", bgColor: "bg-yellow-50", textColor: "text-yellow-600", type: "other" },
            rar: { icon: "", bgColor: "bg-yellow-50", textColor: "text-yellow-600", type: "other" },
        };
        return fileTypes[extension] || { icon: "", bgColor: "bg-gray-100", textColor: "text-gray-600", type: "other" };
    };

    const formatDate = (dateString) => new Date(dateString).toLocaleDateString('en-US', {
        month: 'short',
        day: 'numeric',
        year: 'numeric'
    });

    const getFileStats = () => {
        return uploads.reduce(
            (stats, { fileName }) => {
                const ext = fileName.split('.').pop().toLowerCase();
                if (['pdf', 'doc', 'docx', 'txt'].includes(ext)) stats.documents++;
                else if (['jpg', 'jpeg', 'png', 'gif'].includes(ext)) stats.images++;
                else stats.others++;
                stats.total++;
                return stats;
            },
            { total: 0, documents: 0, images: 0, others: 0 }
        );
    };

    const fileStats = getFileStats();

    const handleFileClick = (file) => {
        const fileType = getFileTypeInfo(file.fileName).type;
        if (fileType === 'image') {
            setSelectedFile(file);
        } else {
            window.open(file.fileUrl, '_blank');
        }
    };

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
            {loading && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white p-6 rounded-lg shadow-xl">
                        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto"></div>
                        <p className="mt-4 text-gray-700">Loading...</p>
                    </div>
                </div>
            )}

            {selectedFile && (
                <div 
                    className="fixed inset-0 bg-black/80 backdrop-blur-sm flex items-center justify-center z-50 p-4"
                    onClick={() => setSelectedFile(null)}
                >
                    <div 
                        className="relative max-w-4xl w-full bg-white rounded-xl shadow-2xl overflow-hidden"
                        onClick={e => e.stopPropagation()}
                    >
                        <div className="p-4 border-b flex justify-between items-center">
                            <h3 className="text-lg font-medium">{selectedFile.description || selectedFile.fileName}</h3>
                            <button 
                                onClick={() => setSelectedFile(null)}
                                className="text-gray-500 hover:text-gray-700 p-2 rounded-full hover:bg-gray-100"
                            >
                                
                            </button>
                        </div>
                        <div className="p-4 bg-gray-100">
                            <img
                                src={selectedFile.fileUrl}
                                alt={selectedFile.description || selectedFile.fileName}
                                className="w-full h-auto max-h-[80vh] object-contain rounded-lg"
                            />
                        </div>
                        <div className="p-4 border-t bg-gray-50">
                            <p className="text-sm text-gray-600">
                                Uploaded on {formatDate(selectedFile.date)}
                            </p>
                        </div>
                    </div>
                </div>
            )}

            {error && (
                <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-6 rounded">
                    <p className="font-bold">Error</p>
                    <p>{error}</p>
                </div>
            )}

            <div className="bg-white rounded-lg shadow-lg overflow-hidden mb-8 p-6">
                <div className="flex flex-col md:flex-row items-center justify-between">
                    <div className="w-full md:w-1/2 mb-6 md:mb-0">
                        <h1 className="text-3xl font-bold text-gray-800 mb-4">Course Materials</h1>
                        <p className="text-gray-600 mb-6">Access all your course materials and resources in one place</p>
                        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                            {[
                                { label: "Total Files", value: fileStats.total, color: "blue" },
                                { label: "Documents", value: fileStats.documents, color: "green" },
                                { label: "Images", value: fileStats.images, color: "purple" },
                                { label: "Others", value: fileStats.others, color: "yellow" },
                            ].map(({ label, value, color }) => (
                                <div key={label} className={`bg-${color}-50 p-4 rounded-lg`}>
                                    <div className={`text-${color}-600 text-2xl font-bold`}>{value}</div>
                                    <div className={`text-${color}-600 text-sm`}>{label}</div>
                                </div>
                            ))}
                        </div>
                    </div>

                    <div className="w-full md:w-1/2 flex justify-center">
                        <img src="/images/Book.avif" alt="Course illustration" className="rounded-lg shadow-lg w-64 h-64" />
                    </div>
                </div>
            </div>

            <div className="bg-white rounded-lg shadow-lg overflow-hidden">
                <div className="p-6 border-b border-gray-200">
                    <h2 className="text-xl font-semibold text-gray-800">Available Materials</h2>
                </div>
                <div className="p-6">
                    {uploads.length === 0 ? (
                        <div className="text-center py-8">
                            <p className="text-gray-500 text-lg">No materials uploaded yet</p>
                        </div>
                    ) : (
                        <div className="grid gap-4">
                            {uploads.map((file) => {
                                const { icon, bgColor, textColor, type } = getFileTypeInfo(file.fileName);
                                return (
                                    <div 
                                        key={file._id} 
                                        className="flex items-center p-4 rounded-lg hover:bg-gray-50 transition-all duration-200 cursor-pointer"
                                        onClick={() => handleFileClick(file)}
                                    >
                                        <div className={`flex items-center justify-center w-12 h-12 ${bgColor} rounded-lg mr-4`}>
                                            <span className="text-2xl">{icon}</span>
                                        </div>
                                        <div className="flex-1 min-w-0">
                                            <p className={`${textColor} font-medium hover:underline truncate`}>
                                                {type === 'image' ? 'View Image' : 'Open File'}
                                            </p>
                                            <div className="mt-1 flex items-center text-sm text-gray-500">
                                                <span className="truncate">{file.description || file.fileName}</span>
                                                <span className="mx-2"></span>
                                                <span>{formatDate(file.date || new Date())}</span>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    )}
                </div>
            </div>
        </main>
    );
};

export default StdGetClass;