import React, { useEffect, useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import classContext from "../../context/classContext";

const Imgarr = [
    "/images/img1.png",
    "/images/img2.png",
    "/images/img3.png",
    "/images/img4.png",
    "/images/img5.png",
    "/images/img6.png",
    "/images/img7.png",
    "/images/img8.png",
    "/images/img9.png",
];

export default function StdMicroIteam() { // React component must start with an uppercase letter
    const navigate = useNavigate();
    const { classes, getClass } = useContext(classContext);
    const [classListWithImages, setClassListWithImages] = useState([]);

    useEffect(() => {
        getClass(); // Fetch classes
    }, [getClass]);

    useEffect(() => {
        if (classes && classes.length > 0) {
            const updatedClassList = classes.map((classItem, index) => ({
                ...classItem,
                randomImage: Imgarr[index % Imgarr.length],
            }));
            setClassListWithImages(updatedClassList);
        }
    }, [classes]);

    const handleMicrolist = (classId) => {
        navigate("/stdmicroiteam", { state: { classId } });
    };

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
            <h1 className="text-2xl text-center font-bold md:-mt-6">Microproject</h1>

            <div className="grid grid-cols-1 sm:grid-cols-1 lg:grid-cols-1 gap-4 mt-6">
                {classListWithImages.length > 0 ? (
                    classListWithImages.map((classItem, index) => (
                        <div key={index} className="grid grid-cols-1 sm:grid-cols-1 lg:grid-cols-1 gap-4">
                            <form
                                className="p-6 bg-[#A0AEC0] border-[#D6BCFA] rounded-lg shadow-[#3B82F6] hover:shadow-lg transition-shadow relative"
                                style={{
                                    backgroundImage: `url(${classItem.randomImage})`,
                                    backgroundSize: "cover",
                                    backgroundRepeat: "no-repeat",
                                    backgroundPosition: "center",
                                }}
                                onClick={() => handleMicrolist(classItem._id)}
                            >
                                <h5 className="text-2xl font-bold text-gray-900 dark:text-white">
                                    {classItem.className}
                                </h5>
                                <p className="text-gray-700">{classItem.branch}</p>

                                <div className="absolute md:top-1/2 top-1/3 right-4 transform -translate-y-1/2">
                                    <h1 className="text-3xl md:text-5xl font-bold text-gray-900 dark:text-white md:-mt-6">
                                        {classItem.subject}
                                    </h1>
                                    <p className="text-gray-700 text-right">{classItem.inviteCode}</p>
                                </div>
                            </form>
                        </div>
                    ))
                ) : (
                    <p className="text-gray-700 text-center mt-4">No classes available</p>
                )}
            </div>
        </main>
    );
}
