import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

function LoginPage() {
  const [isSignUp, setIsSignUp] = useState(false);
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("");

  const toggleForm = () => {
    setIsSignUp((prev) => !prev);
    setMessage("");
    setMessageType("");
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-500/10 to-purple-500/10 p-4">
      <div className="w-full max-w-md">
        <div className="backdrop-blur-md bg-white/80 p-6 sm:p-8 rounded-2xl shadow-2xl border border-white/20">
          <div className="mb-6 text-center">
            <h2 className="text-2xl sm:text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              {isSignUp ? 'Create Account' : 'Welcome Back'}
            </h2>
            <p className="text-gray-600 mt-2 text-sm sm:text-base">
              {isSignUp ? 'Join our community today' : 'Sign in to continue'}
            </p>
          </div>

          {isSignUp ? (
            <SignUpForm setMessage={setMessage} setMessageType={setMessageType} />
          ) : (
            <SignInForm setMessage={setMessage} setMessageType={setMessageType} />
          )}

          <div className="mt-6 text-center">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300"></div>
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-white/80 text-gray-500">
                  {isSignUp ? 'Already registered?' : 'New here?'}
                </span>
              </div>
            </div>

            <button
              onClick={toggleForm}
              className="mt-4 text-blue-600 hover:text-blue-800 transition-colors font-medium text-sm sm:text-base"
            >
              {isSignUp ? 'Sign in to your account' : 'Create a new account'}
            </button>
          </div>

          {message && (
            <div
              className={`mt-4 p-3 rounded-lg text-center text-sm ${messageType === 'success'
                ? 'bg-green-50 text-green-700 border border-green-200'
                : 'bg-red-50 text-red-700 border border-red-200'
                }`}
            >
              {message}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

function InputField({ label, type, placeholder, value, setValue, autoComplete, error }) {
  return (
    <div className="mb-4">
      <label className="block text-gray-700 text-sm font-medium mb-2">
        {label}
      </label>
      <input
        type={type}
        placeholder={placeholder}
        value={value}
        onChange={(e) => setValue(e.target.value)}
        className={`w-full px-3 py-2 rounded-lg border transition-all placeholder-gray-400 
          ${error
            ? 'border-red-300 focus:ring-2 focus:ring-red-500 focus:border-transparent'
            : 'border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent'
          }`}
        required
        autoComplete={autoComplete}
      />
      {error && <p className="text-red-500 text-xs mt-1">{error}</p>}
    </div>
  );
}

function Loader() {
  return (
    <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin" />
  );
}

function SignInForm({ setMessage, setMessageType }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();
    setLoading(true);
    setErrors({});
  
    try {
      const response = await fetch("https://presence-pro-backend.vercel.app/api/auth/login", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });
  
      const result = await response.json();
      console.log(result);
  
      if (response.ok) {
        localStorage.setItem("authToken", result.authtoken);
        localStorage.setItem("username", result.user.username);
        localStorage.setItem("role", result.user.role);

        setMessage("Login successful!");
        setMessageType("success");
        
        if (result.user.role === "teacher") {
          navigate("/home");
        } else {
          navigate("/stdhome");
        }
        
      } else {
        if (result.errors) {
          const serverErrors = {};
          result.errors.forEach(err => {
            serverErrors[err.path] = err.msg;
          });
          setErrors(serverErrors);
          setMessage("Please correct the errors in the form.");
          setMessageType("error");
        } else {
          setMessage(result.error || "Login failed. Please try again.");
          setMessageType("error");
        }
      }
    } catch (error) {
      setMessage("Error connecting to the server.");
      setMessageType("error");
    } finally {
      setLoading(false);
    }
  };
  
  return (
    <form onSubmit={handleLogin} className="space-y-4">
      <InputField
        label="Email Address"
        type="email"
        placeholder="Enter your email"
        value={email}
        setValue={setEmail}
        autoComplete="email"
        error={errors.email}
      />
      <InputField
        label="Password"
        type="password"
        placeholder="Enter your password"
        value={password}
        setValue={setPassword}
        autoComplete="current-password"
        error={errors.password}
      />
      <button
        type="submit"
        className="w-full bg-gradient-to-r from-blue-600 to-blue-700 text-white py-3 rounded-lg hover:from-blue-700 hover:to-blue-800 transition-all flex justify-center items-center space-x-2 font-medium"
        disabled={loading}
      >
        {loading ? <Loader /> : "Sign In"}
      </button>
    </form>
  );
}

function SignUpForm({ setMessage, setMessageType }) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [inviteCode, setInviteCode] = useState("");
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const navigate = useNavigate();

  const handleSignUp = async (e) => {
    e.preventDefault();
    setLoading(true);
    setErrors({});

    try {
      const response = await fetch("https://presence-pro-backend.vercel.app/api/auth/createuser", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username, email, password, inviteCode }),
      });

      const result = await response.json();

      if (response.ok) {
        setMessage("Account created successfully!");
        setMessageType("success");
        navigate("/");
      } else {
        // Specifically handle validation errors from server
        if (result.errors) {
          // Transform server validation errors
          const serverErrors = {};
          result.errors.forEach(err => {
            serverErrors[err.path] = err.msg;
          });
          setErrors(serverErrors);

          // If there are field-specific errors, show a general error message
          setMessage("Please correct the errors in the form.");
          setMessageType("error");
        } else if (result.error && result.error.includes('duplicate key error')) {
          // Handle duplicate email error specifically
          setErrors({ email: 'Email is already in use. Please use a different email.' });
          setMessage('The email you entered is already in use.');
          setMessageType('error');
        } else {
          // Handle general error message from server
          setMessage(result.error || "Sign up failed. Please try again.");
          setMessageType("error");
        }
      }
    } catch (error) {
      setMessage("Error connecting to the server.");
      setMessageType("error");
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSignUp} className="space-y-4">
      <InputField
        label="Full Name"
        type="text"
        placeholder="Enter your full name"
        value={username}
        setValue={setUsername}
        autoComplete="name"
        error={errors.username}
      />
      <InputField
        label="Email Address"
        type="email"
        placeholder="Enter your email"
        value={email}
        setValue={setEmail}
        autoComplete="email"
        error={errors.email}
      />
      <InputField
        label="Password"
        type="password"
        placeholder="Create a password"
        value={password}
        setValue={setPassword}
        autoComplete="new-password"
        error={errors.password}
      />
      <InputField
        label="Invitation Code"
        type="text"
        placeholder="Enter code"
        value={inviteCode}
        setValue={setInviteCode}
        autoComplete="off"
        error={errors.inviteCode}
      />
      <button
        type="submit"
        className="w-full bg-gradient-to-r from-blue-600 to-purple-600 text-white py-3 rounded-lg hover:from-blue-700 hover:to-purple-700 transition-all flex justify-center items-center space-x-2 font-medium"
        disabled={loading}
      >
        {loading ? <Loader /> : "Create Account"}
      </button>
    </form>
  );
}


export default LoginPage;