import React, { useContext, useState } from "react";
import ClassTab from "./ClassTab";
import classContext from "../../context/classContext";

const InputField = ({ type, name, value, onChange, placeholder }) => (
  <div className="relative">
    <input
      type={type}
      name={name}
      value={value}
      onChange={onChange}
      className="w-full px-4 py-3 text-sm font-normal text-gray-700 transition-all bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
      placeholder={placeholder}
      required
    />
  </div>
);

const Loader = () => (
  <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin text-center" />
);

export default function Class() {
  const { addClass } = useContext(classContext);

  const [classDetails, setClassDetails] = useState({
    className: "",
    subject: "",
    inviteCode: "",
    branch: "",
  });

  const [loading, setLoading] = useState(false);

  const handleClick = async (e) => {
    e.preventDefault();

    if (!classDetails.className || !classDetails.subject || !classDetails.inviteCode || !classDetails.branch) {
      alert("Please fill in all fields!");
      return;
    }

    setLoading(true);

    try {
      await addClass(
        classDetails.className,
        classDetails.subject,
        classDetails.inviteCode,
        classDetails.branch
      );

      setClassDetails({ className: "", subject: "", inviteCode: "", branch: "" });
      alert("Class has been added");
    } catch (error) {
      console.error("Error adding class:", error);
      alert("Failed to add class. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const onChange = ({ target: { name, value } }) => {
    setClassDetails((prevDetails) => ({ ...prevDetails, [name]: value }));
  };

  return (
    <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 pt-4 w-full text-gray-700 bg-gray-800 shadow-lg rounded-xl bg-clip-border p-6">
        <div className="col-span-2 text-center">
          <h2 className="text-2xl font-bold text-white">Create Class</h2>
        </div>
        <div className="col-span-2 rounded-lg shadow-md">
          <form className="md:space-y-4 space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <InputField
                type="text"
                name="className"
                value={classDetails.className}
                onChange={onChange}
                placeholder="Class Name Ex..,(CO-5K)"
              />
              <InputField
                type="text"
                name="branch"
                value={classDetails.branch}
                onChange={onChange}
                placeholder="Branch"
              />
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <InputField
                type="text"
                name="subject"
                value={classDetails.subject}
                onChange={onChange}
                placeholder="Subject"
              />
              <InputField
                type="text"
                name="inviteCode"
                value={classDetails.inviteCode}
                onChange={onChange}
                placeholder="Invite Code"
              />
            </div>
            <div className="text-center">
              <button
                type="submit"
                className="w-full px-6 my-3 py-3 text-sm font-bold text-white uppercase bg-[#D6BCFA] rounded-lg shadow-md focus:ring-blue-400 transition-all flex justify-center items-center"
                disabled={loading}
                onClick={handleClick}
              >
                {loading ? <Loader /> : "Create"}
              </button>
            </div>
          </form>
        </div>
      </div>
      <ClassTab />
    </main>
  );
}
