import React, { useState, useEffect, } from 'react';
import { Link } from 'react-router-dom';

export default function Iteam1() {
    // State variables to hold the time, date, and day
    const [time, setTime] = useState("");
    const [date, setDate] = useState("");
    const [day, setDay] = useState("");

    // Function to update the time, date, and day every second
    useEffect(() => {
        // Function to get current time, date, and day
        const updateDateTime = () => {
            const now = new Date();

            // Format time (HH:mm)
            const currentTime = now.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });

            // Format date (DD-MMM-YYYY)
            const currentDate = now.toLocaleDateString('en-GB', { day: '2-digit', month: 'short', year: 'numeric' });

            // Format day (e.g., Monday)
            const currentDay = now.toLocaleDateString('en-GB', { weekday: 'long' });

            // Set the state values
            setTime(currentTime);
            setDate(currentDate);
            setDay(currentDay);
        };

        // Update data every second (1000 milliseconds)
        const intervalId = setInterval(updateDateTime, 1000);

        // Call it once to populate immediately
        updateDateTime();

        // Clear the interval when component is unmounted
        return () => clearInterval(intervalId);
    }, []);

    return (
        <div className="grid grid-cols-2 md:grid-cols-1 gap-4">
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <button className="block p-6 bg-white border border-gray-200 rounded-lg shadow-lg transition-transform transform hover:scale-105 hover:shadow-2xl">
                    <h5 className="mb-2 text-xl sm:text-2xl font-bold tracking-tight text-gray-900">
                        {time}
                    </h5>
                    <p className="font-normal text-gray-700 dark:text-gray-400">
                        Time
                    </p>
                </button>
                <button className="block p-6 bg-white border border-gray-200 rounded-lg shadow-lg transition-transform transform hover:scale-105 hover:shadow-2xl">
                    <h5 className="mb-2 text-xl sm:text-2xl font-bold tracking-tight text-gray-900">
                        {date}
                    </h5>
                    <p className="font-normal text-gray-700 dark:text-gray-400">
                        Date
                    </p>
                </button>
            </div>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <button className="block p-6 bg-white border border-gray-200 rounded-lg shadow-lg transition-transform transform hover:scale-105 hover:shadow-2xl">
                    <h5 className="mb-2 text-xl sm:text-2xl font-bold tracking-tight text-gray-900">
                        {day}
                    </h5>
                    <p className="font-normal text-gray-700 dark:text-gray-400">
                        Day
                    </p>
                </button>
                <Link
                    to="/new"
                    className="group flex flex-col items-center justify-center w-full py-3 text-sm font-medium text-center text-slate-900 border-2 border-dashed rounded-md border-slate-300 hover:border-blue-500 hover:bg-white hover:text-blue-500"
                >
                    <svg
                        className="mb-1 text-slate-400 group-hover:text-blue-500"
                        width="20"
                        height="20"
                        fill="currentColor"
                        aria-hidden="true"
                    >
                        <path d="M10 5a1 1 0 0 1 1 1v3h3a1 1 0 1 1 0 2h-3v3a1 1 0 1 1-2 0v-3H6a1 1 0 1 1 0-2h3V6a1 1 0 0 1 1-1Z" />
                    </svg>
                    Add Event
                </Link>
            </div>
        </div>
    );
}
