import React, { useState, useEffect } from 'react';
import Cookies from 'js-cookie';

export default function AddEventPage() {
    const [eventDetails, setEventDetails] = useState({
        time: '',
        date: '',
        description: ''
    });
    const [events, setEvents] = useState([]);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setEventDetails(prev => ({
            ...prev,
            [name]: value,
        }));
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        if (!eventDetails.time || !eventDetails.date || !eventDetails.description) {
            alert('Please fill out all fields');
            return;
        }

        const eventDate = new Date(eventDetails.date);
        eventDate.setDate(eventDate.getDate() + 5);
        const expiryDate = eventDate.toUTCString();

        const newEvent = { ...eventDetails, expiry: expiryDate };
        setEvents(prev => [...prev, newEvent]);
        Cookies.set('events', JSON.stringify([...events, newEvent]), { expires: 5 });

        setEventDetails({
            time: '',
            date: '',
            description: ''
        });
    };

    useEffect(() => {
        const savedEvents = Cookies.get('events');
        if (savedEvents) {
            setEvents(JSON.parse(savedEvents));
        }
    }, []);

    const formatDate = (dateStr) => {
        return new Date(dateStr).toLocaleDateString('en-US', {
            weekday: 'long',
            year: 'numeric',
            month: 'long',
            day: 'numeric'
        });
    };

    const formatTime = (timeStr) => {
        return new Date(`2000/01/01 ${timeStr}`).toLocaleTimeString('en-US', {
            hour: 'numeric',
            minute: 'numeric'
        });
    };

    const handleDeleteEvent = (index) => {
        const updatedEvents = events.filter((_, i) => i !== index);
        setEvents(updatedEvents);
        Cookies.set('events', JSON.stringify(updatedEvents), { expires: 5 });
    };

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
            <h1 className="text-4xl font-bold text-gray-800 mb-4 md:mb-8 text-center">
                Event Planner
            </h1>

            <div className="bg-white rounded-2xl shadow-xl p-4 md:p-8 md:mb-8 mb-4">
                <h2 className="text-2xl font-semibold text-gray-700 mb-6">Add New Event</h2>
                <form onSubmit={handleSubmit} className="space-y-6">
                    <div className="grid md:grid-cols-2 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2" htmlFor="time">
                                Event Time
                            </label>
                            <input
                                type="time"
                                id="time"
                                name="time"
                                value={eventDetails.time}
                                onChange={handleChange}
                                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2" htmlFor="date">
                                Event Date
                            </label>
                            <input
                                type="date"
                                id="date"
                                name="date"
                                value={eventDetails.date}
                                onChange={handleChange}
                                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                            />
                        </div>
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2" htmlFor="description">
                            Event Description
                        </label>
                        <textarea
                            id="description"
                            name="description"
                            value={eventDetails.description}
                            onChange={handleChange}
                            className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all resize-none"
                            rows="4"
                            placeholder="Describe your event..."
                        ></textarea>
                    </div>
                    <button
                        type="submit"
                        className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 text-white font-semibold py-3 rounded-lg hover:from-blue-700 hover:to-indigo-700 transform hover:scale-[1.02] transition-all duration-200 shadow-md"
                    >
                        Add Event
                    </button>
                </form>
            </div>

            <div className="bg-white rounded-2xl shadow-xl md:p-8 p-4">
                <h2 className="text-2xl font-semibold text-gray-700 mb-6">Upcoming Events</h2>
                <div className="space-y-4">
                    {events.length === 0 ? (
                        <div className="text-center py-8 text-gray-500">
                            No events added yet. Start by adding your first event above!
                        </div>
                    ) : (
                        events.map((event, index) => (
                            <div
                                key={index}
                                className="p-6 border border-gray-100 rounded-xl shadow-sm hover:shadow-md transition-shadow bg-gradient-to-r from-white to-gray-50"
                            >
                                <div className="flex items-start justify-between">
                                    <div>
                                        <h3 className="text-xl font-bold text-gray-800">
                                            {formatTime(event.time)}
                                        </h3>
                                        <p className="text-gray-600 mt-1">
                                            {formatDate(event.date)}
                                        </p>
                                    </div>
                                    <span className="px-3 py-1 bg-blue-100 text-blue-800 text-sm font-medium rounded-full">
                                        Upcoming
                                    </span>
                                </div>
                                <p className="mt-4 text-gray-700 leading-relaxed">
                                    {event.description}
                                </p>
                                <p className="text-sm text-gray-500 mt-4">
                                    Expires: {new Date(event.expiry).toLocaleDateString()}
                                </p>
                                {/* Delete Button for Single Event */}
                                <button
                                    onClick={() => handleDeleteEvent(index)}
                                    className="mt-4 px-4 py-2 bg-red-600 text-white font-semibold rounded-lg hover:bg-red-700 transform hover:scale-[1.05] transition-all duration-200"
                                >
                                    Delete Event
                                </button>
                            </div>
                        ))
                    )}
                </div>
            </div>
        </main>
    );
}
