import React, { useState } from "react";
import microprojectContext from "./microprojectContext";

const MicroprojectState = ({ children }) => {
    const host = "https://presence-pro-backend.vercel.app";
    const [microproject, setMicroproject] = useState([]);

    // Example auth token for requests
    const token = localStorage.getItem("authToken");

    // Utility function for making API requests
    const apiRequest = async (url, method, body = null) => {
        try {
            const headers = {
                "Content-Type": "application/json",
                "auth-token": token,
            };

            const options = { method, headers };
            if (body) options.body = JSON.stringify(body);

            const response = await fetch(url, options);

            if (!response.ok) {
                const errorDetails = await response.json();
                throw new Error(`API request failed: ${response.status} - ${errorDetails.message || response.statusText}`);
            }

            return await response.json();
        } catch (error) {
            console.error("API Error:", error.message || error);
            throw error;
        }
    };

    // Fetch all microprojects by class ID
    const getMicroproject = async (classId) => {
        try {
            const data = await apiRequest(`${host}/api/microproject/microprojectsbyclass`, "POST", { classId });
            setMicroproject(data || []);
        } catch (error) {
            console.error("Error fetching microprojects:", error.message || error);
        }
    };

    // Add a new microproject
    const addMicroproject = async () => {
        try {
            // Log the students data before submission
            console.log("Students array received for submission:", microproject);

            const { title, classId, students } = microproject;

            // Ensure students array is not empty and validate each student
            if (!Array.isArray(students) || students.length === 0) {
                throw new Error("Students ksjarray must not be empty.");
            }

            // Validate each student object
            students.forEach((student, index) => {
                console.log(`Validating student ${index + 1}:`, student);
                if (!student.name || !student.rollNo || !student.class || !student.enrollment) {
                    throw new Error(`Student ${index + 1} is missing required fields.`);
                }
            });

            // Create the payload to match the desired structure
            const newMicroprojectData = {
                title,
                classId,
                students
            };

            // Make the API request to add the new microproject
            const newMicroproject = await apiRequest(
                `${host}/api/microproject/addmicroproject`,
                "POST",
                newMicroprojectData
            );

            // Update state with the new microproject
            setMicroproject((prev) => [...prev, newMicroproject]);
            return newMicroproject;
        } catch (error) {
            console.error("Error adding microproject:", error.message || error);
            throw error;
        }
    };


    // Delete a microproject
    const deleteMicroproject = async (id) => {
        try {
            await apiRequest(`${host}/api/microproject/deletemicroproject/${id}`, "DELETE");
            setMicroproject((prev) => prev.filter((proj) => proj._id !== id));
        } catch (error) {
            console.error("Error deleting microproject:", error.message || error);
            throw error;
        }
    };

    return (
        <microprojectContext.Provider
            value={{
                addMicroproject,
                getMicroproject,
                deleteMicroproject,
                microproject,
                setMicroproject,
            }}
        >
            {children}
        </microprojectContext.Provider>
    );
};

export default MicroprojectState;
