import React, { useEffect, useContext, useState } from "react";
import { useLocation } from "react-router-dom";
import microprojectContext from "../../context/microprojectContext";
import { debounce } from "lodash";

export default function Microlist() {
    const location = useLocation();
    const { classId } = location.state || {};
    const { getMicroproject, microproject } = useContext(microprojectContext);
    const [localMicroproject, setLocalMicroproject] = useState([]);
    const [error, setError] = useState(null);

    // Fetch data only when classId changes
    useEffect(() => {
        if (!classId) return;

        const fetchMicroprojects = debounce(async () => {
            try {
                console.log("Fetching microprojects for classId:", classId);
                await getMicroproject(classId);
            } catch (err) {
                setError(err.message);
            }
        }, 500); // Debounce delay

        fetchMicroprojects();

        return () => fetchMicroprojects.cancel(); // Cleanup function
    }, [classId, getMicroproject]); //  No unnecessary dependencies

    // Update local state only if microproject changes
    useEffect(() => {
        if (classId && JSON.stringify(localMicroproject) !== JSON.stringify(microproject)) {
            console.log("Updated Microprojects:", microproject);
            setLocalMicroproject(Array.isArray(microproject) ? microproject : []);
        }
    }, [microproject, classId, localMicroproject]);

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
            <h1 className="text-3xl font-bold text-gray-800 mb-8 text-center">
                Microproject Dashboard
                {error && <span className="text-red-500"> {error}</span>}
            </h1>

            {localMicroproject.length > 0 ? (
                <div className="space-y-6">
                    {localMicroproject.map((project) => (
                        <div
                            key={project._id}
                            className="bg-white rounded-xl shadow-md overflow-hidden transition-all duration-300 hover:shadow-lg border border-gray-100"
                        >
                            <div className="bg-gradient-to-r from-blue-500 to-blue-600 p-4">
                                <h2 className="text-xl font-semibold text-white">
                                    Title: {project.title}
                                </h2>
                            </div>
                            <div className="p-4">
                                {project.students?.length > 0 ? (
                                    <div className="overflow-x-auto">
                                        <table className="w-full">
                                            <thead>
                                                <tr className="bg-gray-50">
                                                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-600">Name</th>
                                                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-600">Roll No</th>
                                                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-600">Class</th>
                                                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-600">Enrollment</th>
                                                </tr>
                                            </thead>
                                            <tbody className="divide-y divide-gray-100">
                                                {project.students.map((student) => (
                                                    <tr
                                                        key={student.enrollment}
                                                        className="transition-colors hover:bg-gray-50"
                                                    >
                                                        <td className="px-4 py-3 text-sm text-gray-800">{student.name}</td>
                                                        <td className="px-4 py-3 text-sm text-gray-600">{student.rollNo}</td>
                                                        <td className="px-4 py-3 text-sm text-gray-600">{student.class}</td>
                                                        <td className="px-4 py-3 text-sm text-gray-600">{student.enrollment}</td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                ) : (
                                    <p className="text-gray-500 text-center py-4">
                                        No students assigned to this project
                                    </p>
                                )}
                            </div>
                        </div>
                    ))}
                </div>
            ) : (
                <div className="text-center py-12 bg-white rounded-lg shadow">
                    <div className="text-gray-400 mb-3">
                        <svg
                            className="mx-auto h-12 w-12"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                        >
                            <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth="2"
                                d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"
                            />
                        </svg>
                    </div>
                    <p className="text-gray-500">No microprojects available for this class</p>
                </div>
            )}
        </main>
    );
}
