import React, { useState, useEffect, useCallback } from "react";
import { useLocation } from "react-router-dom";
import { Upload, X, Trash2 } from "lucide-react";

const ImageUpload = () => {
    const host = "https://presence-pro-backend.vercel.app";
    const location = useLocation();
    const classId = location.state?.classId;

    const [image, setImage] = useState(null);
    const [description, setDescription] = useState("");
    const [uploads, setUploads] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [modalOpen, setModalOpen] = useState(false);
    const [isUploading, setIsUploading] = useState(false);
    const [selectedImage, setSelectedImage] = useState(null);
    const [deletingId, setDeletingId] = useState(null);

    const fetchUploads = useCallback(async () => {
        if (!classId) return;

        setLoading(true);
        setError(null);
        try {
            const response = await fetch(`${host}/api/upload/fetchalluploads/${classId}`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": localStorage.getItem("authToken"),
                },
            });
            if (!response.ok) throw new Error("Failed to fetch images.");
            const json = await response.json();
            const imageUploads = json.filter(upload => {
                const ext = upload.fileName.split('.').pop().toLowerCase();
                return ['jpg', 'jpeg', 'png', 'gif'].includes(ext);
            });
            setUploads(imageUploads);
        } catch (error) {
            setError(error.message);
        } finally {
            setLoading(false);
        }
    }, [classId]);

    useEffect(() => {
        fetchUploads();
    }, [fetchUploads]);

    const uploadImage = async () => {
        if (!image) {
            setError("Please select an image!");
            return;
        }

        setIsUploading(true);
        setError(null);

        const formData = new FormData();
        formData.append("file", image);
        formData.append("description", description);
        formData.append("classId", classId);

        try {
            const response = await fetch(`${host}/api/upload/uploadfile`, {
                method: "POST",
                headers: {
                    "auth-token": localStorage.getItem("authToken"),
                },
                body: formData,
            });
            if (!response.ok) throw new Error("Failed to upload image.");
            const newUpload = await response.json();
            setUploads((prevUploads) => [...prevUploads, newUpload.savedUpload]);
            setImage(null);
            setDescription("");
            const fileInput = document.querySelector('input[type="file"]');
            if (fileInput) fileInput.value = '';
            setModalOpen(false);
        } catch (error) {
            setError(error.message);
        } finally {
            setIsUploading(false);
        }
    };

    const deleteImage = async (id) => {
        setDeletingId(id);
        try {
            const response = await fetch(`${host}/api/upload/deleteupload/${id}`, {
                method: "DELETE",
                headers: {
                    "auth-token": localStorage.getItem("authToken"),
                },
            });
            if (!response.ok) throw new Error("Failed to delete image.");
            setUploads((prevUploads) => prevUploads.filter(upload => upload._id !== id));
        } catch (error) {
            setError(error.message);
        } finally {
            setDeletingId(null);
        }
    };

    const handleImageChange = (e) => {
        const selectedFile = e.target.files[0];
        if (selectedFile) {
            const validTypes = ['image/jpeg', 'image/png', 'image/gif'];
            if (!validTypes.includes(selectedFile.type)) {
                setError('Please select a valid image file (JPG, PNG, or GIF)');
                e.target.value = '';
                return;
            }

            const maxSizeMB = 5;
            if (selectedFile.size > maxSizeMB * 1024 * 1024) {
                setError(`Image size exceeds ${maxSizeMB}MB! Please select a smaller image.`);
                e.target.value = '';
                return;
            }

            setImage(selectedFile);
            setError(null);
        }
    };

    const LoadingSpinner = () => (
        <div className="flex items-center justify-center">
            <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-blue-500" />
        </div>
    );

    const formatDate = (dateString) => {
        return new Date(dateString).toLocaleDateString('en-US', {
            month: 'short',
            day: 'numeric',
            year: 'numeric'
        });
    };

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-100 md:rounded-s-3xl min-h-screen">
            {loading && (
                <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50">
                    <div className="bg-white p-8 rounded-xl shadow-xl">
                        <LoadingSpinner />
                        <p className="mt-4 text-gray-700">Processing...</p>
                    </div>
                </div>
            )}

            {error && (
                <div className="bg-red-50 border-l-4 border-red-500 text-red-700 p-4 mb-6 rounded">
                    <p className="font-semibold">Error</p>
                    <p>{error}</p>
                </div>
            )}

            {selectedImage && (
                <div 
                    className="fixed inset-0 bg-black/80 backdrop-blur-sm flex items-center justify-center z-50 p-4"
                    onClick={() => setSelectedImage(null)}
                >
                    <div 
                        className="relative max-w-4xl w-full bg-white rounded-xl shadow-2xl overflow-hidden"
                        onClick={e => e.stopPropagation()}
                    >
                        <div className="p-4 border-b flex justify-between items-center">
                            <h3 className="text-lg font-medium">{selectedImage.description}</h3>
                            <button 
                                onClick={() => setSelectedImage(null)}
                                className="text-gray-500 hover:text-gray-700 p-2 rounded-full hover:bg-gray-100"
                            >
                                <X size={20} />
                            </button>
                        </div>
                        <div className="p-4">
                            <img
                                src={selectedImage.fileUrl}
                                alt={selectedImage.description}
                                className="w-full h-auto max-h-[80vh] object-contain rounded-lg"
                            />
                        </div>
                        <div className="p-4 border-t bg-gray-50">
                            <p className="text-sm text-gray-600">
                                Uploaded on {formatDate(selectedImage.date)}
                            </p>
                        </div>
                    </div>
                </div>
            )}

            {modalOpen && (
                <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
                    <div className="bg-white p-6 rounded-xl shadow-xl w-full max-w-md">
                        <div className="flex justify-between items-center mb-6">
                            <h2 className="text-xl font-semibold">Upload Image</h2>
                            <button 
                                onClick={() => setModalOpen(false)}
                                className="text-gray-500 hover:text-gray-700 p-2 rounded-full hover:bg-gray-100"
                            >
                                <X size={20} />
                            </button>
                        </div>

                        <div className="space-y-6">
                            <div className="space-y-2">
                                <label className="block text-sm font-medium text-gray-700">
                                    Image File (Max: 5MB)
                                </label>
                                <input
                                    type="file"
                                    accept="image/*"
                                    onChange={handleImageChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100"
                                />
                            </div>

                            <div className="space-y-2">
                                <label className="block text-sm font-medium text-gray-700">Description</label>
                                <textarea
                                    value={description}
                                    onChange={(e) => setDescription(e.target.value)}
                                    placeholder="Enter image description"
                                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 h-24 resize-none"
                                />
                            </div>

                            <div className="flex gap-3">
                                <button
                                    onClick={uploadImage}
                                    disabled={isUploading || !image}
                                    className="flex-1 bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
                                >
                                    {isUploading ? <LoadingSpinner /> : (
                                        <>
                                            <Upload size={18} />
                                            <span>Upload</span>
                                        </>
                                    )}
                                </button>
                                <button
                                    onClick={() => setModalOpen(false)}
                                    className="flex-1 bg-gray-100 hover:bg-gray-200 text-gray-700 font-medium py-2 px-4 rounded-lg transition-colors"
                                >
                                    Cancel
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            <div className="space-y-6">
                <div className="bg-white rounded-xl shadow-sm overflow-hidden">
                    <div className="p-6">
                        <div className="flex justify-between items-center">
                            <h1 className="text-2xl font-semibold text-gray-800">Upload Notes</h1>
                            <button
                                onClick={() => setModalOpen(true)}
                                className="bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition-colors flex items-center gap-2"
                            >
                                <Upload size={18} />
                                <span>Upload New</span>
                            </button>
                        </div>
                    </div>
                </div>

                <div className="bg-white rounded-xl shadow-sm overflow-hidden">
                    <div className="p-6">
                        {uploads.length === 0 ? (
                            <div className="text-center py-12">
                                <Upload size={48} className="mx-auto text-gray-400 mb-4" />
                                <p className="text-gray-500 text-lg">No images uploaded yet</p>
                                <p className="text-gray-400 text-sm mt-2">Upload your first image to get started</p>
                            </div>
                        ) : (
                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                                {uploads.map((upload) => (
                                    <div
                                        key={upload._id}
                                        className="group relative bg-white rounded-xl border shadow-sm overflow-hidden hover:shadow-md transition-shadow"
                                    >
                                        <div 
                                            className="relative aspect-video cursor-pointer bg-gray-100"
                                            onClick={() => setSelectedImage(upload)}
                                        >
                                            <img 
                                                src={upload.fileUrl} 
                                                alt={upload.description}
                                                className="w-full h-full object-cover"
                                            />
                                            <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                                                <p className="text-white font-medium">View Details</p>
                                            </div>
                                        </div>
                                        <div className="p-4">
                                            <p className="text-sm text-gray-700 line-clamp-2">{upload.description}</p>
                                            <div className="mt-3 flex items-center justify-between">
                                                <span className="text-xs text-gray-500">{formatDate(upload.date)}</span>
                                                <button
                                                    onClick={() => deleteImage(upload._id)}
                                                    disabled={deletingId === upload._id}
                                                    className="text-red-500 hover:text-red-600 p-2 rounded-full hover:bg-red-50 transition-colors disabled:opacity-50"
                                                >
                                                    {deletingId === upload._id ? (
                                                        <LoadingSpinner />
                                                    ) : (
                                                        <Trash2 size={16} />
                                                    )}
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </main>
    );
};

export default ImageUpload;