import React, { useContext, useEffect, useState } from 'react';
import attendanceContext from '../../context/attendanceContext';

const Attendance = () => {
    const [classFilter, setClassFilter] = useState('All Class');
    const [searchTerm, setSearchTerm] = useState('');
    const [debouncedSearchTerm, setDebouncedSearchTerm] = useState('');

    const { attendance, getStdAttendance } = useContext(attendanceContext);

    // Fetch attendance only when the component mounts
    useEffect(() => {
        const fetchData = async () => {
            try {
                await getStdAttendance();
                console.log("Data fetched");
            } catch (err) {
                alert("Failed to fetch attendance");
            }
        };
        fetchData();
    }, [getStdAttendance]); // Empty dependency array ensures this runs only once

    // Debounce search term
    useEffect(() => {
        const timer = setTimeout(() => {
            setDebouncedSearchTerm(searchTerm);
        }, 500);
        return () => clearTimeout(timer);
    }, [searchTerm]);

    const formatDate = (date) => {
        return new Date(date).toLocaleString('en-US', {
            year: 'numeric',
            month: 'short',
            day: 'numeric',
            hour: 'numeric',
            minute: '2-digit',
            hour12: true
        });
    };

    const classNames = ['All Class', ...new Set(attendance.map(item => item.className))];

    const filteredAttendance = attendance.filter((item) =>
        (classFilter === 'All Class' || item.className === classFilter) &&
        (!debouncedSearchTerm ||
            item.className.toLowerCase().includes(debouncedSearchTerm.toLowerCase()) ||
            item.subject.toLowerCase().includes(debouncedSearchTerm.toLowerCase()) ||
            item.attendanceCode.toLowerCase().includes(debouncedSearchTerm.toLowerCase()) ||
            (item.date && formatDate(item.date).toLowerCase().includes(debouncedSearchTerm.toLowerCase()))
        )
    );

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
            <div className="max-w-7xl mx-auto">
                <div className="flex flex-col rounded-xl lg:flex-row lg:items-center lg:justify-between space-y-4 lg:space-y-0 mb-8">
                    <h1 className="text-2xl font-bold text-gray-800">Attendance Management</h1>
                    <div className="flex flex-col sm:flex-row gap-4">
                        <div className="relative">
                            <input
                                type="text"
                                placeholder="Search..."
                                className="w-full sm:w-64 pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                            />
                            <svg
                                className="absolute left-3 top-2.5 h-5 w-5 text-gray-400"
                                fill="none"
                                stroke="currentColor"
                                viewBox="0 0 24 24"
                            >
                                <path
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                    strokeWidth="2"
                                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                                />
                            </svg>
                        </div>
                        <select
                            className="w-full sm:w-48 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                            value={classFilter}
                            onChange={(e) => setClassFilter(e.target.value)}
                        >
                            {classNames.map((className) => (
                                <option key={className} value={className}>{className}</option>
                            ))}
                        </select>
                    </div>
                </div>

                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead>
                            <tr className="bg-gray-100">
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">#</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Class Name</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Subject</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Date</th>
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {filteredAttendance.length > 0 ? (
                                filteredAttendance.map((item, index) => (
                                    <tr key={`${item.id}-${item.date}`} className="hover:bg-gray-50 transition-colors">
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{index + 1}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{item.className}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{item.subject}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{formatDate(item.date)}</td>
                                    </tr>
                                ))
                            ) : (
                                <tr>
                                    <td colSpan="5" className="px-6 py-4 text-center text-sm text-gray-500">
                                        No attendance records found
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>
            </div>
        </main>
    );
};

export default Attendance;
