import React from "react";
import ClassState from "./ClassState";
import AttendanceState from "./AttendanceState";
import MicroprojectState from "./MicroprojectState";
import StdMicroprojectState from "./StdMicroprojectState";
import CloudinaryState from "./CloudinaryState";

export const AppContextProvider = ({ children }) => {
    return (
        <ClassState>
            <AttendanceState>
                <MicroprojectState>
                    <StdMicroprojectState>
                        <CloudinaryState>
                            {children}
                        </CloudinaryState>
                    </StdMicroprojectState>
                </MicroprojectState>
            </AttendanceState>
        </ClassState>
    );
};