import './App.css';
import React, { useEffect } from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useLocation,
  Navigate
} from 'react-router-dom';

import Sidebar from './components/Teacher/Sidebar';
import About from './components/static/About';
import Home from './components/Teacher/Home';
import Contact from './components/static/Contact';
import Class from './components/Teacher/Class';
import Microproject from './components/Teacher/Microproject';
import Microlist from './components/Teacher/Microlist';
import Calendar from './components/static/Calender';
import LoginPage from './pages/LoginPage';
import Event from './components/static/Event';
import Attendance from './components/Teacher/Attendance';

import StdHome from './components/student/StdHome';
import StdClass from './components/student/StdClass';
import StdMicroprIteam from './components/student/StdMicroprIteam';
import StdAttendance from './components/student/StdAttendance';
import AddMicroproject from './components/student/AddMicroproject';
import StdGetClass from './components/student/StdGetClass';

import ClassUpload from './components/Teacher/ClassUpload';
import GoogleSheetGuide from './components/static/GoogleSheetGuide';
import GoogleSheetFrontend from './components/Teacher/GoogleSheetFrontend';

import { AppContextProvider } from './context/AppContextProvider';
import RoleRoute from './components/RoleRoute';
import Unauthorized from './pages/Unauthorized';

function App() {
  useEffect(() => {
    const style = document.createElement('style');
    style.textContent = `
      html, body {
        overflow-y: scroll;
      }
      ::-webkit-scrollbar {
        display: none;
      }
    `;
    document.head.appendChild(style);
    return () => document.head.removeChild(style);
  }, []);

  return (
    <Router>
      <AppContextProvider>
        <MainContent />
      </AppContextProvider>
    </Router>
  );
}

function MainContent() {
  const location = useLocation();
  const token = localStorage.getItem("authToken");

  // Sidebar only when logged in
  const showSidebar = token && location.pathname !== "/login" && location.pathname !== "/";

  return (
    <>
      {showSidebar && <Sidebar />}

      <Routes>
        {/* ---------- PUBLIC ---------- */}
        <Route path="/" element={<LoginPage />} />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/unauthorized" element={<Unauthorized />} />


        {/* ---------- PROTECTED (TEACHER) ---------- */}
        <Route path="/dashboard" element={<RoleRoute allowedRole="teacher"><Home /></RoleRoute>} />
        <Route path="/home" element={<RoleRoute allowedRole="teacher"><Home /></RoleRoute>} />
        <Route path="/class" element={<RoleRoute allowedRole="teacher"><Class /></RoleRoute>} />
        <Route path="/attendance" element={<RoleRoute allowedRole="teacher"><Attendance /></RoleRoute>} />
        <Route path="/microproject" element={<RoleRoute allowedRole="teacher"><Microproject /></RoleRoute>} />
        <Route path="/microlist" element={<RoleRoute allowedRole="teacher"><Microlist /></RoleRoute>} />
        <Route path="/classupload" element={<RoleRoute allowedRole="teacher"><ClassUpload /></RoleRoute>} />
        <Route path="/absentstudent" element={<RoleRoute allowedRole="teacher"><GoogleSheetFrontend /></RoleRoute>} />
        <Route path="/googleSheet" element={<RoleRoute allowedRole="teacher"><GoogleSheetGuide /></RoleRoute>} />

        {/* ---------- PROTECTED (STUDENT) ---------- */}
        <Route path="/stdhome" element={<RoleRoute allowedRole="student"><StdHome /></RoleRoute>} />
        <Route path="/stdclass" element={<RoleRoute allowedRole="student"><StdClass /></RoleRoute>} />
        <Route path="/stdmicroproject" element={<RoleRoute allowedRole="student"><StdMicroprIteam /></RoleRoute>} />
        <Route path="/stdmicroiteam" element={<RoleRoute allowedRole="student"><AddMicroproject /></RoleRoute>} />
        <Route path="/stdattendance" element={<RoleRoute allowedRole="student"><StdAttendance /></RoleRoute>} />
        <Route path="/stdgetclass" element={<RoleRoute allowedRole="student"><StdGetClass /></RoleRoute>} />

        {/* ---------- SEMI-PUBLIC ---------- */}
        <Route path="/about" element={<About />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/calendar" element={<Calendar />} />
        <Route path="/new" element={<Event />} />

        {/* ---------- FALLBACK ---------- */}
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    </>
  );
}

export default App;
