import React, { useState } from 'react';
import emailjs from 'emailjs-com';
import { Helmet } from 'react-helmet';

const Contact = () => {
    const [formData, setFormData] = useState({
        from_name: '',
        to_name: 'Admin', // Hardcoded or dynamic, depends on your setup
        email: '',
        message: '',
    });

    // Handle form input changes
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    // Handle form submission
    const handleSubmit = (e) => {
        e.preventDefault();

        // Log form data to verify the inputs
        console.log('Form Data:', formData);

        // Send email using EmailJS
        emailjs
            .sendForm(
                'service_ijmdetj', // Your EmailJS service ID
                'template_up5qsk4', // Your EmailJS template ID
                e.target, // Reference to the form
                'bjfMv-o3-IG9C5qoY' // Your EmailJS user ID
            )
            .then(
                (result) => {
                    // Log success result and show success message
                    console.log('Email sent successfully:', result.text);
                    alert('Message sent successfully!');
                },
                (error) => {
                    // Log error and show error message
                    console.error('Error sending email:', error.text);
                    alert('Failed to send the message, please try again.');
                }
            );

        // Reset the form after submission
        setFormData({
            from_name: '',
            to_name: 'Admin', // Set back to default
            email: '',
            message: '',
        });
    };

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200  md:rounded-s-3xl">
            <Helmet>
                <title>contact | PresencePro</title>
                <meta name="description" content="MarkIn dashboard for managing attendance efficiently." />
                <link rel="canonical" href="https://presence-pro-front-end2.vercel.app/contact" />
            </Helmet>
            <section className="bg-gray-100 md:py-16 py-10 px-4 rounded-lg">
                <div className="max-w-4xl mx-auto text-center">
                    <h2 className="text-3xl font-bold text-gray-800">Contact Us</h2>
                    <p className="mt-4 text-lg text-gray-600">
                        Have questions? We're here to help. Fill out the form below and we'll get back to you as soon as possible.
                    </p>

                    <form onSubmit={handleSubmit} className="mt-8 space-y-6">
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                            <div>
                                <label htmlFor="from_name" className="block text-sm font-medium text-gray-700">Your Name</label>
                                <input
                                    type="text"
                                    id="from_name"
                                    name="from_name"
                                    value={formData.from_name}
                                    onChange={handleChange}
                                    required
                                    className="mt-2 p-3 w-full border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500"
                                    placeholder="Your Name"
                                />
                            </div>
                            <div>
                                <label htmlFor="email" className="block text-sm font-medium text-gray-700">Your Email</label>
                                <input
                                    type="email"
                                    id="email"
                                    name="email"
                                    value={formData.email}
                                    onChange={handleChange}
                                    required
                                    className="mt-2 p-3 w-full border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500"
                                    placeholder="Your Email"
                                />
                            </div>
                        </div>

                        <div>
                            <label htmlFor="message" className="block text-sm font-medium text-gray-700">Your Message</label>
                            <textarea
                                id="message"
                                name="message"
                                value={formData.message}
                                onChange={handleChange}
                                required
                                rows="4"
                                className="mt-2 p-3 w-full border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500"
                                placeholder="Your Message"
                            ></textarea>
                        </div>

                        <div className="mt-6">
                            <button
                                type="submit"
                                className="w-full bg-indigo-600 text-white py-3 rounded-md hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                            >
                                Send Message
                            </button>
                        </div>
                    </form>
                </div>
            </section>
        </main>
    );
};

export default Contact;
