import { useState } from "react";
import Cookies from "js-cookie"; 

const CodeForm = ({ onSuccess }) => {
  const [formData, setFormData] = useState({
    className: "",
    subject: "",
    date: "",
    attendacneCode: "", // Updated to match backend field name
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate empty fields
    if (!formData.className || !formData.subject || !formData.date || !formData.attendacneCode) {
      alert(" All fields are required.");
      return;
    }

    try {
      const response = await fetch("https://presence-pro-backend.vercel.app/api/attendance/checkcode", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("authToken"),
        },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (response.ok) {
        setFormData({
          className: "",
          subject: "",
          date: "",
          attendacneCode: "",
        })
        alert(` ${data.message}`);
        onSuccess(); // Trigger the onSuccess prop after successful code verification
      } else {
        setFormData({
          className: "",
          subject: "",
          date: "",
          attendacneCode: "",
        })
        alert(` ${data.error || "Failed to validate attendance code."}`);
      }

      if (data.url) {
        Cookies.set("attendanceUrl", data.url, { expires: 1 });
      }
      
    } catch (error) {
      console.error("Error checking attendance:", error);
      alert(" Server error. Please try again later.");
    }
  };

  return (
    <div className="relative flex flex-col w-full max-w-lg mx-auto text-gray-700 bg-[#2D3748] shadow-lg rounded-xl bg-clip-border ">
      <h2 className="text-xl sm:text-2xl font-semibold text-white pt-4 text-center">
        Register Attendance
      </h2>
      <form onSubmit={handleSubmit} className="grid grid-cols-1 sm:grid-cols-2 gap-4 p-4 sm:p-6">
        <div className="relative">
          <input
            name="className"
            value={formData.className}
            onChange={handleChange}
            className="w-full px-3 sm:px-4 py-2 sm:py-3 text-sm font-normal text-gray-700 bg-gray-100 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none"
            placeholder="Class Name (e.g., CO-3K)"
            required
          />
        </div>
        <div className="relative">
          <input
            name="subject"
            value={formData.subject}
            onChange={handleChange}
            className="w-full px-3 sm:px-4 py-2 sm:py-3 text-sm font-normal text-gray-700 bg-gray-100 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none"
            placeholder="Subject"
            required
          />
        </div>
        <div className="relative">
          <input
            type="date"
            name="date"
            value={formData.date}
            onChange={handleChange}
            className="w-full px-3 sm:px-4 py-2 sm:py-3 text-sm font-normal text-gray-700 bg-gray-100 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none"
            required
          />
        </div>
        <div className="relative">
          <input
            name="attendacneCode"
            value={formData.attendacneCode}
            onChange={handleChange}
            className="w-full px-3 sm:px-4 py-2 sm:py-3 text-sm font-normal text-gray-700 bg-gray-100 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none"
            placeholder="Attendance Code"
            minLength={8}
            required
          />
        </div>
        <div className="col-span-1 sm:col-span-2 ">
          <button
            className="w-full px-4 sm:px-6 py-2 sm:py-3 text-sm font-bold text-white uppercase bg-blue-600 rounded-lg shadow-md hover:bg-blue-700 focus:ring-2 focus:ring-blue-400 transition-all"
            type="submit"
          >
            Register
          </button>
        </div>
      </form>
    </div>
  );
};

export default CodeForm;
