import React, { useContext, useState } from 'react';
import Tab from '../static/Tab';
import Iteam1 from '../static/Iteam1';
import AttendanceForm from './AttendanceForm';
import attendanceContext from '../../context/attendanceContext';
import { useNavigate } from "react-router-dom";
import { Helmet } from "react-helmet";

export default function Home() {
  const context = useContext(attendanceContext);
  const { addAttendance } = context;
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const [attendance, setAttendance] = useState({
    className: '',
    subject: '',
    date: '',
    attendacneCode: ''
  });

  const handleClick = async (e) => {
    e.preventDefault();

    // Validation: Check if all fields are filled
    if (!attendance.className || !attendance.subject || !attendance.date || !attendance.attendacneCode) {
      alert('Please fill in all fields!');
      return;
    }

    setLoading(true); // Start loading indicator
    try {
      // Call addAttendance from context
      await addAttendance(
        attendance.className,
        attendance.subject,
        attendance.date,
        attendance.attendacneCode
      );

      // Reset form fields after successful submission
      setAttendance({ className: '', subject: '', date: '', attendacneCode: '' });
      alert('Attendance has been added successfully!');

      navigate("/absentstudent");

    } catch (error) {
      console.error('Error adding attendance:', error);

      // Show error message to the user
      alert(error.message || 'Failed to add attendance. Please try again.');
    } finally {
      setLoading(false); // Stop loading indicator
    }
  };

  const onChange = ({ target: { name, value } }) => {
    setAttendance((prevDetails) => ({ ...prevDetails, [name]: value }));
  };

  return (
    <div>
      <Helmet>
        <title>Dashboard | PresencePro</title>
        <meta name="description" content="MarkIn dashboard for managing attendance efficiently." />
        <link rel="canonical" href="https://presence-pro-front-end2.vercel.app/home" />
      </Helmet>

      <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
        <div className="">
          <Iteam1 />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 md:mt-7 mt-4 md:gap-6 gap-3 pt-3 w-full text-gray-700 bg-[#A0AEC0] shadow-lg rounded-xl bg-clip-border md:p-6 p-3 ">
          {/* Form Section 1 */}
          <AttendanceForm />

          {/* Form Section 2 */}
          <div className="relative flex flex-col w-full max-w-lg mx-auto text-gray-700 bg-[#2D3748] shadow-lg rounded-xl bg-clip-border">
            <h2 className="text-2xl font-semibold text-white pt-4 text-center">
              Available To Student
            </h2>
            <form className="grid grid-cols-1 sm:grid-cols-2 gap-4 p-6" >
              <div className="relative">
                <input
                  name="className"
                  value={attendance.className}
                  onChange={onChange}
                  className="w-full px-4 py-3 text-sm font-normal text-gray-700 transition-all bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
                  placeholder="Class Name (e.g., CO-3K)"
                />
              </div>
              <div className="relative">
                <input
                  name="subject"
                  value={attendance.subject}
                  onChange={onChange}
                  className="w-full px-4 py-3 text-sm font-normal text-gray-700 transition-all bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
                  placeholder="Subject"
                />
              </div>
              <div className="relative">
                <input
                  type="date"
                  name="date"
                  value={attendance.date}
                  onChange={onChange}
                  className="w-full px-4 py-3 text-sm font-normal text-gray-700 transition-all bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
                />
              </div>
              <div className="relative">
                <input
                  name="attendacneCode"
                  value={attendance.attendacneCode}
                  onChange={onChange}
                  className="w-full px-4 py-3 text-sm font-normal text-gray-700 transition-all bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
                  placeholder="Attendance Code"
                  minLength={8}
                />
              </div>
              <div className="col-span-1 sm:col-span-2">
                <button
                  className="w-full px-6 py-3 text-sm font-bold text-white uppercase bg-blue-600 rounded-lg shadow-md hover:bg-blue-700 focus:ring-2 focus:ring-blue-400 transition-all"
                  type="submit"
                  disabled={loading}
                  onClick={handleClick}
                >
                  {loading ? 'Processing...' : 'Available'}
                </button>
              </div>
            </form>
          </div>
        </div>
        <div>
          <Tab />
        </div>
      </main>
    </div>
  );
}
