import React, { useState } from 'react';

export default function Calendar() {
    const [currentDate, setCurrentDate] = useState(new Date());

    const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
    const months = [
        'January', 'February', 'March', 'April', 'May', 'June',
        'July', 'August', 'September', 'October', 'November', 'December'
    ];

    const getDaysInMonth = (month, year) => {
        return new Date(year, month + 1, 0).getDate();
    };

    const changeMonth = (direction) => {
        const newDate = new Date(currentDate.setMonth(currentDate.getMonth() + direction));
        setCurrentDate(newDate);
    };

    const generateCalendar = () => {
        const year = currentDate.getFullYear();
        const month = currentDate.getMonth();
        const daysInMonth = getDaysInMonth(month, year);
        const firstDay = new Date(year, month, 1).getDay();

        const calendarDays = [];
        for (let i = 0; i < firstDay; i++) {
            calendarDays.push(<div key={`empty-${i}`} className="h-12"></div>);
        }
        for (let day = 1; day <= daysInMonth; day++) {
            calendarDays.push(
                <div key={day} className="h-12 flex items-center justify-center border">
                    {day}
                </div>
            );
        }
        return calendarDays;
    };

    return (
        <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
            <div className="p-4 bg-gray-100 rounded-lg shadow-md">
                <div className="flex justify-between items-center mb-4">
                    <button
                        onClick={() => changeMonth(-1)}
                        className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
                    >
                        Prev
                    </button>
                    <h2 className="text-lg font-semibold">
                        {months[currentDate.getMonth()]} {currentDate.getFullYear()}
                    </h2>
                    <button
                        onClick={() => changeMonth(1)}
                        className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
                    >
                        Next
                    </button>
                </div>
                <div className="grid grid-cols-7 gap-2">
                    {daysOfWeek.map((day) => (
                        <div key={day} className="h-12 flex items-center justify-center font-bold">
                            {day}
                        </div>
                    ))}
                    {generateCalendar()}
                </div>
            </div>
        </main>
    );
}
