import React,{useState} from "react";
import StdMicroprojectContext from "./StdMicroprojectContext";

export const StdMicroprojectState = ({ children }) => {
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [message, setMessage] = useState(null);
    const addMicroproject = async (formData) => {
        setLoading(true);
        setError(null);
        setMessage(null);
        try {
            const response = await fetch("https://presence-pro-backend.vercel.app/api/microproject/addmicroproject", {
                method: "POST",
                headers: { "Content-Type": "application/json",
                    "auth-token": localStorage.getItem("authToken"),
                 },
                body: JSON.stringify(formData),
            });
            const data = await response.json();

            if (!response.ok) throw new Error(data.error || "Failed to add microproject");
            setMessage("Microproject added successfully!");
        } catch (err) {
            setError(err.message);
        } finally {
            setLoading(false);
        }
    };

    return (
        <StdMicroprojectContext.Provider value={{ addMicroproject, loading, error, message }}>
            {children}
        </StdMicroprojectContext.Provider>
    );
};

export default StdMicroprojectState;