import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import Navbar from "./Navbar";
import ProfileModal from "./ProfileModal";

const Sidebar = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [profilePhoto, setProfilePhoto] = useState(null);
  const sidebarRef = useRef(null);

  useEffect(() => {
    const savedPhoto = localStorage.getItem("profilePhoto");
    if (savedPhoto) {
      setProfilePhoto(savedPhoto);
    }
  }, []);

  const handleSavePhoto = (photo) => {
    setProfilePhoto(photo);
    localStorage.setItem("profilePhoto", photo);
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (sidebarRef.current && !sidebarRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  let username = localStorage.getItem("username");
  const role = localStorage.getItem("role");

  let StudentSection = " ";
  let TeacherSection = " ";
  let Homeurl = "home ";
  if (role === "student") {
    TeacherSection = "hidden"
    Homeurl = "stdhome"
  } else {
    StudentSection = "hidden"
    Homeurl = "home"
  }

  const handleLinkClick = () => {
    setIsOpen(false);
  };

  return (
    <>
      <div className="fixed top-0 left-0 right-0 bg-white shadow-md lg:hidden z-40">
        <div className="container mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-4">
              <button
                onClick={() => setIsOpen(!isOpen)}
                className="p-2 rounded-lg hover:bg-gray-100 transition-colors lg:hidden"
                aria-label="Toggle menu"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-6 w-6 text-gray-600"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M4 6h16M4 12h16M4 18h16"
                  />
                </svg>
              </button>
              <h1 className="text-xl font-bold text-gray-800">PresencePro</h1>
            </div>

            <div className="flex items-center">
              <div className="flex items-center space-x-3">
                <img
                  src={profilePhoto}
                  alt="Profile"
                  className="w-8 h-8 rounded-full cursor-pointer object-cover border border-gray-200"
                  onClick={() => setIsModalOpen(true)}
                />
                <div className="hidden md:block">
                  <p className="text-sm font-medium text-gray-700">{username}</p>
                  <button
                    onClick={() => setIsModalOpen(true)}
                    className="text-xs text-blue-600 hover:text-blue-800"
                  >
                    Edit Profile
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="flex">
        <div
          ref={sidebarRef}
          className={`fixed inset-y-0 left-0 transform transition-transform duration-300 ease-in-out 
            ${isOpen ? "translate-x-0" : "-translate-x-full"} 
            w-60 bg-[#A0AEC0] text-[#2D3748] lg:translate-x-0 z-50 rounded-e-3xl flex flex-col justify-between`}
        >
          <div>
            <div className="p-3">
              <h2 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
                PresencePro
              </h2>
            </div>
            <div className="border-t">
              <nav className="mt-4 space-y-4">
                <Link
                  to="/home"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${TeacherSection}`}
                >
                  Dashboard
                </Link>
                <Link
                  to="/class"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${TeacherSection}`}
                >
                  Class
                </Link>
                <Link
                  to="/attendance"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${TeacherSection}`}
                >
                  Attendance
                </Link>
                <Link
                  to="/absentstudent"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${TeacherSection}`}
                >
                  Absent Marker
                </Link>
                <Link
                  to="/microproject"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${TeacherSection}`}
                >
                  Microproject
                </Link>
                <Link
                  to="/googleSheet"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${TeacherSection}`}
                >
                  Google Sheet Guide
                </Link>
                <Link
                  to="/calendar"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${TeacherSection}`}
                >
                  Calendar
                </Link>
              </nav>
              <nav className="mt-4 space-y-4">
                <Link
                  to="/stdhome"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${StudentSection}`}
                >
                  Dashboard
                </Link>
                <Link
                  to="/stdclass"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${StudentSection}`}
                >
                  Class
                </Link>
                <Link
                  to="/stdattendance"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${StudentSection}`}
                >
                  Attendance
                </Link>
                <Link
                  to="/stdmicroproject"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${StudentSection}`}
                >
                  Microproject
                </Link>
                <Link
                  to="/calendar"
                  onClick={handleLinkClick}
                  className={`block bg-[#e5e7eb] mr-6 text-left px-6 py-3 hover:bg-[#d8e0ef] rounded-e-xl ${StudentSection}`}
                >
                  Calendar
                </Link>
              </nav>
            </div>
          </div>

          <div className="p-4 border-t border-gray-300 flex items-center">
            <img
              src={profilePhoto}
              alt="Profile"
              className="w-10 h-10 rounded-full mr-4 cursor-pointer"
              onClick={() => setIsModalOpen(true)}
            />
            <div>
              <p className="font-semibold">{username}</p>
              <button
                onClick={() => setIsModalOpen(true)}
                className="text-sm text-blue-500 hover:underline"
              >
                Edit Photo
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Bottom Navigation for Mobile */}
      <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 lg:hidden z-40">
        <div className="flex justify-around items-center h-16">
          <Link
            to={`/${Homeurl}`}
            className="flex flex-col items-center px-4 py-2 text-gray-600 hover:text-blue-600"
          >
            <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
            </svg>
            <span className="text-xs mt-1">Home</span>
          </Link>

          <Link
            to="/contact"
            className="flex flex-col items-center px-4 py-2 text-gray-600 hover:text-blue-600"
          >
            <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
            </svg>
            <span className="text-xs mt-1">Contact</span>
          </Link>

          <Link
            to="/about"
            className="flex flex-col items-center px-4 py-2 text-gray-600 hover:text-blue-600"
          >
            <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
            <span className="text-xs mt-1">About</span>
          </Link>
        </div>
      </div>

      {isModalOpen && (
        <ProfileModal
          onClose={() => setIsModalOpen(false)}
          onSave={handleSavePhoto}
        />
      )}

      <Navbar />
    </>
  );
};

export default Sidebar;