import React, { useState, useEffect } from "react";

const AbsentMarker = () => {
  const [scriptURL, setScriptURL] = useState("");
  const [sheets, setSheets] = useState([]);
  const [selectedSheet, setSelectedSheet] = useState("");
  const [selectedDate, setSelectedDate] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  // Fetch script URL from localStorage on component mount
  useEffect(() => {
    const url = localStorage.getItem("URI");
    if (!url) {
      setMessage("Error: Attendance script URL not found!");
      return;
    }
    setScriptURL(url);
  }, []);

  // Fetch sheet names when scriptURL is available
  useEffect(() => {
    if (!scriptURL) return;

    const fetchSheets = async () => {
      setLoading(true);
      try {
        const response = await fetch(`${scriptURL}?action=getSheets`);
        const data = await response.json();
        if (data.sheetNames) {
          setSheets(data.sheetNames);
        } else {
          setMessage("Error: No sheets found.");
        }
      } catch (error) {
        console.error("Error fetching sheets:", error);
        setMessage("Error fetching sheets. Please try again.");
      } finally {
        setLoading(false);
      }
    };

    fetchSheets();
  }, [scriptURL]);

  // Convert date to DD/MM/YYYY format
  const formatDate = (date) => {
    if (!date) return "";
    const d = new Date(date);
    return `${("0" + d.getDate()).slice(-2)}/${("0" + (d.getMonth() + 1)).slice(-2)}/${d.getFullYear()}`;
  };

  // Function to handle marking absent
  const markAbsent = async () => {
    if (!selectedSheet || !selectedDate) {
      setMessage("Please select a sheet and date!");
      return;
    }

    setLoading(true);
    setMessage("");

    const formData = new FormData();
    formData.append("sheetName", selectedSheet);
    formData.append("date", formatDate(selectedDate)); // Convert date
    formData.append("fillAbsent", "true");

    try {
      const response = await fetch(scriptURL, {
        method: "POST",
        body: formData,
      });
      const result = await response.json();

      if (result.result === "success") {
        setMessage(" Success: All empty cells marked as 'A'!");
      } else {
        setMessage(` Error: ${result.error}`);
      }
    } catch (error) {
      setMessage(" Request failed. Please try again.");
      console.error("Error:", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
      <div className="flex justify-center w-full">
        <div className="bg-white p-4 md:p-6 rounded-lg shadow-lg w-full max-w-sm text-center">
          <h2 className="text-xl md:text-2xl font-semibold text-gray-800">Mark Absent (A)</h2>

          {/* Sheet Selector */}
          <div className="mt-3 md:mt-4">
            <label className="block text-gray-700 text-sm md:text-base">Select Sheet:</label>
            <select
              className="w-full mt-1 p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300 text-sm md:text-base"
              value={selectedSheet}
              onChange={(e) => setSelectedSheet(e.target.value)}
              disabled={loading}
            >
              <option value="">-- Select Sheet --</option>
              {sheets.map((sheet, index) => (
                <option key={index} value={sheet}>
                  {sheet}
                </option>
              ))}
            </select>
          </div>

          {/* Date Selector */}
          <div className="mt-3 md:mt-4">
            <label className="block text-gray-700 text-sm md:text-base">Select Date:</label>
            <input
              type="date"
              className="w-full mt-1 p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300 text-sm md:text-base"
              value={selectedDate}
              onChange={(e) => setSelectedDate(e.target.value)}
              disabled={loading}
            />
          </div>

          {/* Mark Absent Button */}
          <button
            onClick={markAbsent}
            disabled={loading}
            className={`mt-4 md:mt-6 w-full ${
              loading ? "bg-blue-400 cursor-not-allowed" : "bg-blue-600 hover:bg-blue-700"
            } text-white py-2 px-4 rounded-lg font-semibold transition duration-200 text-sm md:text-base`}
          >
            {loading ? "Processing..." : "Mark Absent"}
          </button>

          {/* Message Display */}
          {message && (
            <p className={`mt-3 md:mt-4 text-xs md:text-sm font-semibold ${message.includes("Error") ? "text-red-600" : "text-green-600"}`}>
              {message}
            </p>
          )}
        </div>
      </div>
    </main>
  );
};

export default AbsentMarker;