import React, { useContext, useEffect, useState, useCallback, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import classContext from "../../context/classContext";

function ClassTab() {
    const navigate = useNavigate();
    const { classes, getClass, deleteClass } = useContext(classContext);
    const [classListWithImages, setClassListWithImages] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    //  Memoized Image Array to prevent recreation
    const Imgarr = useMemo(() => [
        "/images/img1.png",
        "/images/img2.png",
        "/images/img3.png",
        "/images/img4.png",
        "/images/img5.png",
        "/images/img6.png",
        "/images/img7.png",
        "/images/img8.png",
        "/images/img9.png",
    ], []);

    //  Memoized getClass function
    const fetchClasses = useCallback(async () => {
        try {
            await getClass();
            console.log("Reloaded");
        } catch (err) {
            setError("Failed to fetch classes");
        } finally {
            setLoading(false);
        }
    }, [getClass]);

    // Fetch classes only once when component mounts
    useEffect(() => {
        fetchClasses();
    }, [fetchClasses]); //  Now dependency is stable

    // Update local state when classes change
    useEffect(() => {
        if (classes && classes.length > 0) {
            const updatedClassList = classes.map((classItem, index) => ({
                ...classItem,
                randomImage: Imgarr[index % Imgarr.length],
            }));
            setClassListWithImages(updatedClassList);
        }
    }, [classes, Imgarr]); //  Included Imgarr safely

    // Handle delete for a class item
    const handleDelete = (id) => {
        deleteClass(id);
        setClassListWithImages((prev) => prev.filter((classItem) => classItem._id !== id));
    };

    if (loading) {
        return <p className="text-center text-gray-500">Loading...</p>;
    }

    if (error) {
        return <p className="text-center text-red-500">{error}</p>;
    }

    const handleClass = (classId) => {
        navigate("/classupload", { state: { classId } });
    };

    return (
        <div className="grid grid-cols-1 sm:grid-cols-1 lg:grid-cols-1 gap-4 mt-6">
            {classListWithImages.length > 0 ? (
                classListWithImages.map((classItem, index) => (
                    <div key={index} className="grid grid-cols-1 sm:grid-cols-1 lg:grid-cols-1 gap-4">
                        <form
                            className="p-4 bg-[#A0AEC0] border-[#D6BCFA] rounded-lg shadow-[#3B82F6] hover:shadow-lg transition-shadow relative cursor-pointer"
                            style={{
                                backgroundImage: `url(${classItem.randomImage})`,
                                backgroundSize: "cover",
                                backgroundRepeat: "no-repeat",
                                backgroundPosition: "center",
                            }}
                            onClick={() => handleClass(classItem._id)}
                        >
                            <h5 className="text-2xl font-bold text-gray-900 dark:text-white">
                                {classItem.className}
                            </h5>
                            <p className="text-gray-700">{classItem.branch}</p>
                            <p className="text-gray-700 mt-4">Student {classItem.students.length}</p>

                            <div className="absolute md:top-1/2 top-1/3 right-4 transform -translate-y-1/2">
                                <h1 className="text-3xl md:text-5xl font-bold text-gray-900 dark:text-white md:-mt-6">
                                    {classItem.subject}
                                </h1>
                                <p className="text-gray-700 text-right">{classItem.inviteCode}</p>
                            </div>

                            <div className="absolute right-4 bottom-2">
                                <i
                                    className="fa-solid fa-trash cursor-pointer text-white hover:text-red-700 text-xl"
                                    title="Delete"
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        handleDelete(classItem._id);
                                    }}
                                ></i>
                            </div>
                        </form>
                    </div>
                ))
            ) : (
                <p className="text-gray-700 text-center mt-4">No classes available</p>
            )}
        </div>
    );
}

export default ClassTab;
