import React, { useState, useEffect } from 'react';

const AttendanceForm = () => {
  const [sheetNames, setSheetNames] = useState([]);
  const [selectedSheet, setSelectedSheet] = useState('');
  const [date, setDate] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [scriptURL, setScriptURL] = useState('');

  useEffect(() => {
    fetch("https://presence-pro-backend.vercel.app/api/uploadurl/fetchurl", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("authToken"),
      },
    })
      .then(response => response.json())
      .then(data => {
        if (data.length > 0) {
          setScriptURL(data[0].url);
          localStorage.setItem("URI", data[0].url )
           // Store script URL in local storage
        }
      })
      .catch(error => console.error("Error fetching script URL:", error));
  }, []);

  useEffect(() => {
    if (!scriptURL) return;
    setIsLoading(true);

    fetch(`${scriptURL}?action=getSheets`)
      .then(response => response.json())
      .then(data => {
        setSheetNames(data.sheetNames);
        setIsLoading(false);
      })
      .catch(error => {
        setErrorMessage('Failed to load sheet names. Please try again.');
        setIsLoading(false);
      });
  }, [scriptURL]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    if (!scriptURL) {
      alert("No script URL available!");
      setIsSubmitting(false);
      return;
    }

    const formData = new FormData();
    formData.append('sheetName', selectedSheet);
    formData.append('newDate', date);

    try {
      const response = await fetch(scriptURL, { method: 'POST', body: formData });
      const result = await response.json();

      if (result.result === 'success') {
        alert(' Success: ' + (result.message || 'Attendance marked successfully!'));
      } else {
        alert(' Error: ' + result.error);
      }
    } catch (error) {
      alert(' Error submitting the form. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="relative flex flex-col w-full max-w-lg mx-auto text-gray-700 bg-[#2D3748] shadow-lg rounded-xl p-6">
      <h2 className="text-2xl font-semibold text-white text-center mb-4">
        Mark Attendance on Google Sheet
      </h2>

      {isLoading && <p className="text-center text-white">Loading sheet names...</p>}
      {errorMessage && <p className="text-center text-red-500">{errorMessage}</p>}

      <form onSubmit={handleSubmit} className="grid gap-4">
        <div>
          <select
            value={selectedSheet}
            onChange={(e) => setSelectedSheet(e.target.value)}
            className="w-full px-4 py-3 text-gray-700 bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
          >
            <option value="">Select Subject</option>
            {sheetNames.length === 0 && !isLoading && (
              <option disabled>No sheets available</option>
            )}
            {sheetNames.map((sheetName, index) => (
              <option key={index} value={sheetName}>
                {sheetName}
              </option>
            ))}
          </select>
        </div>

        <div>
          <input
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            className="w-full px-4 py-3 text-gray-700 bg-gray-100 border rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
          />
        </div>

        <button
          className="w-full px-6 py-3 text-white font-bold bg-blue-600 rounded-lg shadow-md hover:bg-blue-700 focus:ring-2 focus:ring-blue-400 transition-all flex justify-center items-center"
          type="submit"
          disabled={isSubmitting}
        >
          {isSubmitting ? (
            <>
              <span className="loader mr-2"></span> Marking...
            </>
          ) : (
            'Mark Attendance'
          )}
        </button>
      </form>
    </div>
  );
};

export default AttendanceForm;
