import { useState } from "react";

export default function GoogleSheetGuide() {
  const [url, setUrl] = useState("");
  const [copied, setCopied] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch("https://presence-pro-backend.vercel.app/api/uploadurl/uploadurlofsheet", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("authToken"),
        },
        body: JSON.stringify({ url })
      });

      const data = await response.json();
      alert(`Response: ${data.message}`);
    } catch (error) {
      console.error("Error submitting URL:", error);
      alert("Failed to submit URL");
    }
  };


  const codeString = `// Store the spreadsheet ID in script properties
var scriptProp = PropertiesService.getScriptProperties();

// Initialize the script by storing the active spreadsheet ID
function initialSetup() {
  var activeSpreadsheet = SpreadsheetApp.getActiveSpreadsheet();
  scriptProp.setProperty('key', activeSpreadsheet.getId());
}

// Handle GET requests (e.g., fetching sheet names)
function doGet(e) {
  if (e.parameter.action === 'getSheets') {
    return getSheetNames();
  }
  return ContentService.createTextOutput(JSON.stringify({ error: 'Invalid action' }))
    .setMimeType(ContentService.MimeType.JSON);
}

// Fetch all sheet names from the spreadsheet
function getSheetNames() {
  var doc = SpreadsheetApp.openById(scriptProp.getProperty('key'));
  var sheets = doc.getSheets();
  var sheetNames = sheets.map(sheet => sheet.getName());

  return ContentService
    .createTextOutput(JSON.stringify({ sheetNames: sheetNames }))
    .setMimeType(ContentService.MimeType.JSON);
}

// Handle POST requests (e.g., marking attendance)
function doPost(e) {
  var lock = LockService.getScriptLock();
  lock.tryLock(10000); // Lock to prevent concurrent access

  try {
    Logger.log('Request received: ' + JSON.stringify(e.parameter));

    var doc = SpreadsheetApp.openById(scriptProp.getProperty('key'));
    var sheetName = e.parameter.sheetName;
    var sheet = doc.getSheetByName(sheetName);

    if (!sheet) {
      throw new Error('Sheet ' + sheetName + ' not found.');
    }

    var inputDate = e.parameter.date;
    var rollNo = e.parameter.rollNo;
    var newDate = e.parameter.newDate;
    var fillAbsent = e.parameter.fillAbsent;

    // Add a new date to the sheet if requested
    if (newDate) {
      var formattedNewDate = convertToDDMMYYYY(newDate);
      addDateToFirstEmptyCell(sheet, formattedNewDate);
      return ContentService
        .createTextOutput(JSON.stringify({ result: 'success', message: 'New date added successfully to sheet ' + sheetName }))
        .setMimeType(ContentService.MimeType.JSON);
    }

    // Fill empty cells with "A" for a specific date if requested
    if (fillAbsent) {
      var formattedInputDate = convertToDDMMYYYY(inputDate);
      fillEmptyCellsWithA(sheet, formattedInputDate);
      return ContentService
        .createTextOutput(JSON.stringify({ result: 'success', message: 'Empty cells filled with "A" for date ' + formattedInputDate }))
        .setMimeType(ContentService.MimeType.JSON);
    }

    // Mark attendance for a specific roll number and date
    if (!inputDate || !rollNo) {
      throw new Error('Missing date or roll number.');
    }

    var formattedInputDate = convertToDDMMYYYY(inputDate);
    var rollNumbers = sheet.getRange(2, 2, sheet.getLastRow() - 1).getValues().flat();
    var studentRowIndex = rollNumbers.indexOf(parseInt(rollNo)) + 2;

    if (studentRowIndex < 2) {
      throw new Error('Roll number ' + rollNo + ' not found.');
    }

    var dates = sheet.getRange(1, 3, 1, sheet.getLastColumn() - 2).getValues().flat();
    var dateColumnIndex = findDateColumnIndex(dates, formattedInputDate);

    if (dateColumnIndex < 3) {
      throw new Error('Date ' + formattedInputDate + ' not found.');
    }

    sheet.getRange(studentRowIndex, dateColumnIndex).setValue('P'); // Mark as present
    Logger.log('Attendance marked successfully for Roll No: ' + rollNo + ' on Date: ' + formattedInputDate);

    return ContentService
      .createTextOutput(JSON.stringify({ result: 'success', row: studentRowIndex, column: dateColumnIndex, sheetName: sheetName }))
      .setMimeType(ContentService.MimeType.JSON);

  } catch (error) {
    Logger.log('Error occurred: ' + error.message);
    return ContentService
      .createTextOutput(JSON.stringify({ result: 'error', error: error.message }))
      .setMimeType(ContentService.MimeType.JSON);
  } finally {
    lock.releaseLock();
  }
}

// Convert date to DD/MM/YYYY format
function convertToDDMMYYYY(dateString) {
  var dateObj = new Date(dateString);
  var day = ('0' + dateObj.getDate()).slice(-2);
  var month = ('0' + (dateObj.getMonth() + 1)).slice(-2);
  var year = dateObj.getFullYear();
  return day/month/year;
}

// Find the column index of a specific date
function findDateColumnIndex(dates, formattedDate) {
  for (var i = 0; i < dates.length; i++) {
    var sheetFormattedDate = convertToDDMMYYYY(dates[i]);
    if (sheetFormattedDate === formattedDate) {
      return i + 3;
    }
  }
  return -1;
}

// Add a new date to the first empty cell in the header row
function addDateToFirstEmptyCell(sheet, formattedDate) {
  var lastColumn = sheet.getLastColumn();
  var dateRange = sheet.getRange(1, 3, 1, lastColumn - 2).getValues().flat();

  for (var i = 0; i < dateRange.length; i++) {
    if (!dateRange[i]) {
      sheet.getRange(1, i + 3).setValue(formattedDate);
      Logger.log('New date added: ' + formattedDate + ' at column ' + (i + 3));
      return;
    }
  }

  sheet.getRange(1, lastColumn + 1).setValue(formattedDate);
  Logger.log('New date added at the end: ' + formattedDate);
}

// Fill empty cells with "A" for a given date column
function fillEmptyCellsWithA(sheet, formattedDate) {
  var lastRow = sheet.getLastRow();
  var lastColumn = sheet.getLastColumn();

  var dates = sheet.getRange(1, 3, 1, lastColumn - 2).getValues().flat();
  var dateColumnIndex = findDateColumnIndex(dates, formattedDate);

  if (dateColumnIndex < 3) {
    throw new Error('Date ' + formattedDate + ' not found.');
  }

  var columnRange = sheet.getRange(2, dateColumnIndex, lastRow - 1);
  var values = columnRange.getValues();

  for (var i = 0; i < values.length; i++) {
    if (!values[i][0]) {
      values[i][0] = 'A';
    }
  }

  columnRange.setValues(values);
  Logger.log('All empty cells in column ' + dateColumnIndex + ' filled with "A".');
}

`;

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(codeString);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error('Failed to copy text: ', err);
    }
  };

  return (
    <main className="p-4 md:p-8 md:ml-64 md:mt-4 mt-16 md:mb-1 mb-16 bg-gray-200 md:rounded-s-3xl">
      <h2 className="text-3xl font-bold text-gray-800 mb-6">
        How to Get Your Google Apps Script URL
      </h2>

      <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
        <ol className="list-decimal list-inside text-gray-700 space-y-4">
          <li className="pl-2">Open your Google Sheets document</li>
          <li className="pl-2">Click on "Extensions" in the menu</li>
          <li className="pl-2">Select "Apps Script" from the dropdown</li>
          <li className="pl-2">Once the Apps Script editor opens, paste the following code:
            <div className="relative mt-3 mb-3">
              <pre className="bg-gray-50 p-4 rounded-lg font-mono text-sm overflow-x-auto border border-gray-200">
                {codeString}
              </pre>
              <button
                onClick={handleCopy}
                className="absolute top-2 right-2 px-3 py-1 text-sm bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-md transition-colors duration-200"
              >
                {copied ? "Copied!" : "Copy"}
              </button>
            </div>
          </li>
          <li className="pl-2">Click on "Deploy" in the top-right corner</li>
          <li className="pl-2">Choose "New deployment," set access to "Anyone," and click "Deploy"</li>
          <li className="pl-2">Copy the provided URL after deployment</li>
        </ol>
      </div>

      <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">
          Example Google Sheet
        </h3>
        <p className="text-gray-600 mb-4">
          Please ensure your sheet follows this format:
        </p>
        <img
          src="/images/Example.png"
          alt="Example Google Sheet"
          className="w-full rounded-lg shadow-md"
        />
      </div>

      <form onSubmit={handleSubmit} className="bg-white rounded-lg shadow-sm p-6">
        <div className="space-y-4">
          <input
            type="url"
            placeholder="Paste your Google Apps Script URL here"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            required
          />
          <button
            type="submit"
            className="w-full bg-blue-600 text-white py-3 px-4 rounded-lg hover:bg-blue-700 transition-colors duration-200 font-medium"
          >
            Submit URL
          </button>
        </div>
      </form>
    </main>
  );
}